import { useState, useEffect } from 'react';
import { motion } from 'framer-motion';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { RadioGroup, RadioGroupItem } from '@/components/ui/radio-group';
import { Label } from '@/components/ui/label';
import { 
  FileText, 
  Database, 
  Download, 
  CheckCircle, 
  AlertCircle,
  Calendar,
  Users,
  Save,
  AlertTriangle,
  DollarSign
} from 'lucide-react';
import { formatCurrency } from '@/utils/currency';

interface ExportSummaryProps {
  group: any;
  expenses: any[];
  members: any[];
  user: any;
  onExport: (format: string) => Promise<void>;
  loading: boolean;
}

export default function ExportSummary({ 
  group, 
  expenses, 
  members, 
  user,
  onExport,
  loading
}: ExportSummaryProps) {
  const [selectedFormat, setSelectedFormat] = useState('csv');
  const [savedFormat, setSavedFormat] = useState('csv');
  const [exportStatus, setExportStatus] = useState<'idle' | 'exporting' | 'success' | 'error'>('idle');
  const [saveStatus, setSaveStatus] = useState<'idle' | 'saving' | 'success' | 'error'>('idle');

  // Load saved export format from localStorage on component mount
  useEffect(() => {
    try {
      const savedFormat = localStorage.getItem('splitmate_export_format');
      if (savedFormat && ['csv', 'json', 'pdf'].includes(savedFormat)) {
        setSelectedFormat(savedFormat);
        setSavedFormat(savedFormat);
      }
    } catch (error) {
      console.warn('Could not load saved export format from localStorage:', error);
    }
  }, []);

  // Save export format to localStorage
  const handleSaveFormat = () => {
    setSaveStatus('saving');
    try {
      localStorage.setItem('splitmate_export_format', selectedFormat);
      setSavedFormat(selectedFormat);
      setSaveStatus('success');
      // Reset status after 2 seconds
      setTimeout(() => setSaveStatus('idle'), 2000);
    } catch (error) {
      console.warn('Could not save export format to localStorage:', error);
      setSaveStatus('error');
      // Reset status after 2 seconds
      setTimeout(() => setSaveStatus('idle'), 2000);
    }
  };

  const handleExport = async () => {
    setExportStatus('exporting');
    try {
      await onExport(selectedFormat);
      setExportStatus('success');
      // Reset status after 3 seconds
      setTimeout(() => setExportStatus('idle'), 3000);
    } catch (error) {
      console.error('Export error:', error);
      setExportStatus('error');
      // Reset status after 3 seconds
      setTimeout(() => setExportStatus('idle'), 3000);
    }
  };

  // Calculate export statistics
  const totalExpenses = expenses.length;
  const totalMembers = members.length;
  const totalSpent = expenses.reduce((sum, expense) => sum + expense.amount, 0);
  const daysActive = group ? Math.ceil((new Date().getTime() - new Date(group.created_date).getTime()) / (1000 * 60 * 60 * 24)) : 0;

  const formatOptions = [
    { 
      id: 'csv', 
      value: 'csv', 
      label: 'CSV Format', 
      description: 'Best for spreadsheets and data analysis',
      icon: FileText
    },
    { 
      id: 'json', 
      value: 'json', 
      label: 'JSON Format', 
      description: 'Best for developers and API integration',
      icon: Database
    },
    { 
      id: 'pdf', 
      value: 'pdf', 
      label: 'PDF Report', 
      description: 'Professional report with charts and summaries',
      icon: FileText
    }
  ];

  const isFormatChanged = selectedFormat !== savedFormat;

  return (
    <motion.div
      initial={{ opacity: 0, y: 20 }}
      animate={{ opacity: 1, y: 0 }}
      transition={{ duration: 0.5 }}
    >
      <Card className="bg-gradient-to-br from-gray-900/80 to-gray-800/50 border-gray-700/50 backdrop-blur-xl shadow-2xl">
        <CardHeader className="pb-4">
          <CardTitle className="text-white flex items-center gap-3 text-2xl">
            <div className="p-2 bg-gradient-to-r from-emerald-500/20 to-blue-500/20 rounded-lg">
              <Download className="h-6 w-6 text-emerald-400" />
            </div>
            Export Group Data
          </CardTitle>
          <p className="text-gray-400 mt-2">
            Download your group data in various formats for backup or analysis
          </p>
        </CardHeader>
        <CardContent className="space-y-6">
          {/* Data Statistics */}
          <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
            <motion.div 
              className="text-center p-4 bg-gray-800/30 rounded-xl border border-gray-700/50"
              whileHover={{ scale: 1.03 }}
              transition={{ type: "spring", stiffness: 400, damping: 10 }}
            >
              <Calendar className="h-6 w-6 text-cyan-400 mx-auto mb-2" />
              <p className="text-2xl font-bold text-cyan-400">{daysActive}</p>
              <p className="text-gray-400 text-sm">Days Active</p>
            </motion.div>
            <motion.div 
              className="text-center p-4 bg-gray-800/30 rounded-xl border border-gray-700/50"
              whileHover={{ scale: 1.03 }}
              transition={{ type: "spring", stiffness: 400, damping: 10 }}
            >
              <DollarSign className="h-6 w-6 text-green-400 mx-auto mb-2" />
              <p className="text-xl font-bold text-green-400 truncate">{formatCurrency(totalSpent, group?.currency || 'USD', user?.currency_display || 'symbol')}</p>
              <p className="text-gray-400 text-sm">Total Spent</p>
            </motion.div>
            <motion.div 
              className="text-center p-4 bg-gray-800/30 rounded-xl border border-gray-700/50"
              whileHover={{ scale: 1.03 }}
              transition={{ type: "spring", stiffness: 400, damping: 10 }}
            >
              <FileText className="h-6 w-6 text-purple-400 mx-auto mb-2" />
              <p className="text-2xl font-bold text-purple-400">{totalExpenses}</p>
              <p className="text-gray-400 text-sm">Total Expenses</p>
            </motion.div>
            <motion.div 
              className="text-center p-4 bg-gray-800/30 rounded-xl border border-gray-700/50"
              whileHover={{ scale: 1.03 }}
              transition={{ type: "spring", stiffness: 400, damping: 10 }}
            >
              <Users className="h-6 w-6 text-yellow-400 mx-auto mb-2" />
              <p className="text-2xl font-bold text-yellow-400">{totalMembers}</p>
              <p className="text-gray-400 text-sm">Active Members</p>
            </motion.div>
          </div>

          {/* Export Format Selection */}
          <div className="space-y-4">
            <div className="flex items-center justify-between">
              <Label className="text-white font-medium text-lg">Export Format</Label>
              {isFormatChanged && (
                <Button
                  onClick={handleSaveFormat}
                  disabled={saveStatus === 'saving'}
                  variant="outline"
                  className="flex items-center gap-2 border-emerald-500/50 text-emerald-400 hover:bg-emerald-500/10"
                >
                  {saveStatus === 'saving' ? (
                    <>
                      <motion.div
                        animate={{ rotate: 360 }}
                        transition={{ duration: 1, repeat: Infinity, ease: "linear" }}
                        className="h-4 w-4 border-2 border-emerald-500/30 border-t-emerald-500 rounded-full"
                      />
                      <span>Saving...</span>
                    </>
                  ) : saveStatus === 'success' ? (
                    <>
                      <CheckCircle className="h-4 w-4 text-emerald-400" />
                      <span>Saved!</span>
                    </>
                  ) : saveStatus === 'error' ? (
                    <>
                      <AlertTriangle className="h-4 w-4 text-red-400" />
                      <span>Error</span>
                    </>
                  ) : (
                    <>
                      <Save className="h-4 w-4" />
                      <span>Save Changes</span>
                    </>
                  )}
                </Button>
              )}
            </div>
            <RadioGroup 
              value={selectedFormat} 
              onValueChange={setSelectedFormat}
              className="space-y-3"
            >
              {formatOptions.map((option) => {
                const IconComponent = option.icon;
                return (
                  <motion.div
                    key={option.id}
                    whileHover={{ x: 4 }}
                    transition={{ type: "spring", stiffness: 400, damping: 10 }}
                  >
                    <div className={`flex items-center space-x-3 p-4 rounded-xl border-2 transition-all duration-300 ${
                      selectedFormat === option.value
                        ? 'border-emerald-500/50 bg-emerald-500/10'
                        : 'border-gray-700/50 bg-gray-800/30 hover:border-gray-600'
                    }`}>
                      <RadioGroupItem 
                        value={option.value} 
                        id={option.id} 
                        className="border-gray-600 text-emerald-500"
                      />
                      <Label htmlFor={option.id} className="flex items-center space-x-3 cursor-pointer flex-1">
                        <div className={`p-2 rounded-lg ${
                          selectedFormat === option.value
                            ? 'bg-emerald-500/20 text-emerald-400'
                            : 'bg-gray-700/50 text-gray-400'
                        }`}>
                          <IconComponent className="h-5 w-5" />
                        </div>
                        <div className="flex-1">
                          <p className={`font-medium ${
                            selectedFormat === option.value ? 'text-emerald-400' : 'text-white'
                          }`}>
                            {option.label}
                          </p>
                          <p className="text-sm text-gray-400">{option.description}</p>
                        </div>
                      </Label>
                    </div>
                  </motion.div>
                );
              })}
            </RadioGroup>
          </div>

          {/* Export Button with Status */}
          <div className="pt-2">
            <Button
              onClick={handleExport}
              disabled={loading || exportStatus === 'exporting'}
              className="w-full bg-gradient-to-r from-emerald-500 to-blue-500 hover:from-emerald-600 hover:to-blue-600 text-white py-6 text-lg font-medium transition-all duration-300"
            >
              {exportStatus === 'exporting' ? (
                <div className="flex items-center gap-3">
                  <motion.div
                    animate={{ rotate: 360 }}
                    transition={{ duration: 1, repeat: Infinity, ease: "linear" }}
                    className="h-5 w-5 border-2 border-white/30 border-t-white rounded-full"
                  />
                  <span>Exporting {selectedFormat.toUpperCase()}...</span>
                </div>
              ) : exportStatus === 'success' ? (
                <div className="flex items-center gap-3">
                  <CheckCircle className="h-5 w-5 text-white" />
                  <span>Export Successful!</span>
                </div>
              ) : exportStatus === 'error' ? (
                <div className="flex items-center gap-3">
                  <AlertCircle className="h-5 w-5 text-white" />
                  <span>Export Failed. Try Again</span>
                </div>
              ) : (
                <div className="flex items-center gap-3">
                  <Download className="h-5 w-5" />
                  <span>Export as {selectedFormat.toUpperCase()}</span>
                </div>
              )}
            </Button>
          </div>

          {/* Export Information */}
          <div className="text-center text-sm text-gray-500 pt-4">
            <p>Your data will be securely exported and downloaded to your device.</p>
            <p className="mt-1">No data is stored on our servers during this process.</p>
          </div>
        </CardContent>
      </Card>
    </motion.div>
  );
}