import React, { createContext, useContext, useEffect, useState, useCallback } from 'react'
import { User, Session, AuthError } from '@supabase/supabase-js'
import { supabase } from '@/lib/supabase'

interface AuthContextType {
  user: User | null
  session: Session | null
  loading: boolean
  signInWithEmail: (email: string, password: string) => Promise<{ error: AuthError | null }>
  signUpWithEmail: (email: string, password: string) => Promise<{ error: AuthError | null }>
  signOut: () => Promise<{ error: AuthError | null }>
}

const AuthContext = createContext<AuthContextType | undefined>(undefined)

export function AuthProvider({ children }: { children: React.ReactNode }) {
  const [user, setUser] = useState<User | null>(null)
  const [session, setSession] = useState<Session | null>(null)
  const [loading, setLoading] = useState(true)

  // Enhanced session handling with proper error handling and timeouts
  useEffect(() => {
    console.log('AuthProvider useEffect running')
    
    let mounted = true
    let timeoutId: NodeJS.Timeout
    
    const checkAuth = async () => {
      try {
        console.log('Checking auth session...')
        
        // Set a timeout to prevent hanging
        const timeoutPromise = new Promise((_, reject) => {
          timeoutId = setTimeout(() => reject(new Error('Auth check timeout')), 5000)
        })
        
        // Get session with timeout
        const sessionPromise = supabase.auth.getSession()
        
        const { data: { session }, error } = await Promise.race([
          sessionPromise,
          timeoutPromise
        ]) as any
        
        clearTimeout(timeoutId)
        
        if (!mounted) return
        
        if (error) {
          console.warn('Auth check error:', error.message)
          // Continue with no session
          setUser(null)
          setSession(null)
        } else if (session) {
          console.log('Session found for user:', session.user?.email)
          setUser(session.user || null)
          setSession(session)
        } else {
          console.log('No active session')
          setUser(null)
          setSession(null)
        }
      } catch (error: any) {
        console.warn('Auth check failed (continuing anyway):', error.message || error)
        if (mounted) {
          setUser(null)
          setSession(null)
        }
      } finally {
        if (mounted) {
          console.log('Auth check complete, setting loading to false')
          setLoading(false)
        }
      }
    }
    
    checkAuth()
    
    // Listen for auth state changes
    const { data: { subscription } } = supabase.auth.onAuthStateChange((_event, session) => {
      console.log('Auth state changed:', _event)
      if (!mounted) return
      
      setSession(session)
      setUser(session?.user || null)
      setLoading(false)
    })
    
    return () => {
      mounted = false
      subscription.unsubscribe()
      if (timeoutId) clearTimeout(timeoutId)
    }
  }, [])

  const signInWithEmail = useCallback(async (email: string, password: string) => {
    try {
      console.log('Attempting to sign in with email:', email)
      const { data, error } = await supabase.auth.signInWithPassword({
        email,
        password
      })
      
      if (error) {
        console.error('Sign in error:', error)
        return { error }
      }
      
      console.log('Sign in successful for user:', data.user?.email)
      setSession(data.session)
      setUser(data.user)
      return { error: null }
    } catch (error) {
      console.error('Error signing in with email:', error)
      return { error: error as AuthError }
    }
  }, [])

  const signUpWithEmail = useCallback(async (email: string, password: string) => {
    try {
      console.log('Attempting to sign up with email:', email)
      const { data, error } = await supabase.auth.signUp({
        email,
        password
      })
      
      if (error) {
        console.error('Sign up error:', error)
        return { error }
      }
      
      console.log('Sign up successful for user:', data.user?.email)
      // For email signups, the session might be null until email confirmation
      setSession(data.session)
      setUser(data.user || null)
      return { error: null }
    } catch (error) {
      console.error('Error signing up with email:', error)
      return { error: error as AuthError }
    }
  }, [])

  const signOut = useCallback(async () => {
    try {
      console.log('Attempting to sign out user:', user?.email)
      const { error } = await supabase.auth.signOut()
      
      if (error) {
        console.error('Sign out error:', error)
        return { error }
      }
      
      console.log('Sign out successful')
      setSession(null)
      setUser(null)
      return { error: null }
    } catch (error) {
      console.error('Error signing out:', error)
      return { error: error as AuthError }
    }
  }, [user])

  const value: AuthContextType = {
    user,
    session,
    loading,
    signInWithEmail,
    signUpWithEmail,
    signOut
  }

  console.log('AuthProvider rendering with:', { user: !!user, session: !!session, loading })

  return (
    <AuthContext.Provider value={value}>
      {children}
    </AuthContext.Provider>
  )
}

export function useAuth() {
  const context = useContext(AuthContext)
  if (context === undefined) {
    throw new Error('useAuth must be used within an AuthProvider')
  }
  return context
}