import { useState, useCallback } from 'react'
import { useAuth } from '@/contexts/AuthContext'
import { supabase } from '@/lib/supabase'

export function useAuthOperations() {
  const [loading, setLoading] = useState(false)
  const [error, setError] = useState<string | null>(null)
  const { signInWithEmail, signUpWithEmail, signOut } = useAuth()

  const handleEmailSignIn = useCallback(async (email: string, password: string) => {
    setLoading(true)
    setError(null)
    
    try {
      console.log('handleEmailSignIn called with:', { email, password })
      const { error } = await signInWithEmail(email, password)
      
      if (error) {
        console.error('Sign in error:', error)
        // Provide more user-friendly error messages
        if (error.message.includes('Failed to fetch')) {
          setError('Network error: Unable to connect to authentication service. Please check your internet connection.')
        } else if (error.message.includes('Invalid login credentials')) {
          setError('Invalid email or password. Please try again.')
        } else {
          setError(error.message || 'An error occurred during sign in.')
        }
      } else {
        console.log('Sign in successful')
      }
    } catch (err) {
      console.error('Unexpected error in handleEmailSignIn:', err)
      setError('An unexpected error occurred. Please try again.')
    } finally {
      setLoading(false)
    }
  }, [signInWithEmail])

  const handleEmailSignUp = useCallback(async (email: string, password: string) => {
    setLoading(true)
    setError(null)
    
    try {
      console.log('handleEmailSignUp called with:', { email, password })
      const { error } = await signUpWithEmail(email, password)
      
      if (error) {
        console.error('Sign up error:', error)
        // Provide more user-friendly error messages
        if (error.message.includes('Failed to fetch')) {
          setError('Network error: Unable to connect to authentication service. Please check your internet connection.')
        } else if (error.message.includes('already registered')) {
          setError('This email is already registered. Please sign in instead.')
        } else {
          setError(error.message || 'An error occurred during sign up.')
        }
      } else {
        console.log('Sign up successful')
        setError('Check your email for verification link!')
      }
    } catch (err) {
      console.error('Unexpected error in handleEmailSignUp:', err)
      setError('An unexpected error occurred. Please try again.')
    } finally {
      setLoading(false)
    }
  }, [signUpWithEmail])

  const handleSignOut = useCallback(async () => {
    setLoading(true)
    setError(null)
    
    try {
      console.log('handleSignOut called')
      const { error } = await signOut()
      
      if (error) {
        console.error('Sign out error:', error)
        setError(error.message || 'An error occurred during sign out.')
      } else {
        console.log('Sign out successful')
      }
    } catch (err) {
      console.error('Unexpected error in handleSignOut:', err)
      setError('An unexpected error occurred. Please try again.')
    } finally {
      setLoading(false)
    }
  }, [signOut])

  const handleChangePassword = useCallback(async (currentPassword: string, newPassword: string) => {
    setLoading(true)
    setError(null)
    
    try {
      const { data: { user } } = await supabase.auth.getUser()
      
      if (!user?.email) {
        throw new Error('User not found')
      }
      
      // Re-authenticate with current password
      const { error: signInError } = await supabase.auth.signInWithPassword({
        email: user.email,
        password: currentPassword
      })
      
      if (signInError) {
        throw new Error('Current password is incorrect')
      }
      
      // Update password
      const { error: updateError } = await supabase.auth.updateUser({
        password: newPassword
      })
      
      if (updateError) {
        throw new Error(updateError.message || 'Failed to update password')
      }
      
      return { success: true, error: null }
    } catch (err: any) {
      console.error('Password change error:', err)
      const errorMessage = err.message || 'An unexpected error occurred. Please try again.'
      setError(errorMessage)
      return { success: false, error: errorMessage }
    } finally {
      setLoading(false)
    }
  }, [])

  const clearError = useCallback(() => {
    console.log('Clearing error')
    setError(null)
  }, [])

  return {
    loading,
    error,
    handleEmailSignIn,
    handleEmailSignUp,
    handleSignOut,
    handleChangePassword,
    clearError
  }
}