// Member Entity - TypeScript interface and Supabase CRUD operations

import { supabase } from '@/lib/supabase'

export interface Member {
  id: string;

  group_id: string;
  name: string;
  email?: string;
  phone?: string;
  avatar_color: string;
  is_current_user: boolean;
  is_admin: boolean;
  created_date: string;
  created_by: string;
  user_id?: string; // Optional for non-registered members
}

// CRUD operations with Supabase
export class Member {
  static async list(sort?: string): Promise<Member[]> {
    try {
      // Add a timeout to prevent hanging
      const timeoutPromise = new Promise((_, reject) => 
        setTimeout(() => reject(new Error('Members loading timeout')), 3000)
      );
      
      const dataPromise = (async () => {
        try {
          const { data: { user } } = await supabase.auth.getUser();
          
          // If no user, return empty array instead of throwing error
          if (!user) {
            console.warn('No authenticated user found, returning empty members list');
            return [];
          }

          // Query members
          let query = supabase
            .from('members')
            .select('*');

          if (sort === '-created_date') {
            query = query.order('created_date', { ascending: false });
          }
          
          const { data, error } = await query;
          
          if (error) {
            console.warn('Members query error:', error.message);
            return [];
          }
          
          // Update current user info from Supabase auth
          const updatedMembers = (data || []).map(member => {
            if (member.user_id === user.id) {
              return {
                ...member,
                name: user.user_metadata?.full_name || user.email?.split('@')[0] || member.name,
                email: user.email || member.email,
                is_current_user: true,
                user_id: user.id
              }
            }
            return member
          })
          
          return updatedMembers;
        } catch (error) {
          console.warn('Members data fetch error:', error);
          return [];
        }
      })();
      
      const result = await Promise.race([dataPromise, timeoutPromise]);
      return result as Member[];
    } catch (error) {
      console.warn('Error fetching members (returning empty array):', error);
      // Return empty array instead of throwing error to prevent blank page
      return [];
    }
  }
  
  static async create(data: Omit<Member, 'id' | 'created_date' | 'created_by'>): Promise<Member> {
    try {
      const { data: { user } } = await supabase.auth.getUser();
      
      // If no user, throw a more descriptive error
      if (!user) {
        throw new Error('User not authenticated. Please sign in to create a member.');
      }

      // Check if the user is the owner of the group
      const { data: groupData, error: groupError } = await supabase
        .from('groups')
        .select('user_id')
        .eq('id', data.group_id)
        .single();

      if (groupError) {
        throw new Error(`Error checking group ownership: ${groupError.message}`);
      }

      if (groupData.user_id !== user.id) {
        throw new Error('Only group owners can add members to a group.');
      }

      const newMember = {
        ...data,
        created_date: new Date().toISOString(),
        created_by: user.email || 'unknown'
      };

      const { data: insertedData, error } = await supabase
        .from('members')
        .insert(newMember)
        .select()
        .single();
    
      if (error) throw error;
    
      return insertedData;
    } catch (error) {
      console.error('Error creating member:', error);
      // Re-throw the error so it can be handled by the calling function
      throw error;
    }
  }
  
  static async update(id: string, data: Partial<Member>): Promise<Member> {
    try {
      const { data: { user } } = await supabase.auth.getUser();
      
      // If no user, throw a more descriptive error
      if (!user) {
        throw new Error('User not authenticated. Please sign in to update a member.');
      }

      // Check if the user is the owner of the group that the member belongs to
      const { data: memberData, error: memberError } = await supabase
        .from('members')
        .select('group_id')
        .eq('id', id)
        .single();

      if (memberError) {
        throw new Error(`Error finding member: ${memberError.message}`);
      }

      const { data: groupData, error: groupError } = await supabase
        .from('groups')
        .select('user_id')
        .eq('id', memberData.group_id)
        .single();

      if (groupError) {
        throw new Error(`Error checking group ownership: ${groupError.message}`);
      }

      if (groupData.user_id !== user.id) {
        throw new Error('Only group owners can update members in a group.');
      }

      const { data: updatedData, error } = await supabase
        .from('members')
        .update(data)
        .eq('id', id)
        .select()
        .single();
    
      if (error) throw error;
      if (!updatedData) throw new Error('Member not found');
    
      return updatedData;
    } catch (error) {
      console.error('Error updating member:', error);
      // Re-throw the error so it can be handled by the calling function
      throw error;
    }
  }
  
  static async delete(id: string): Promise<void> {
    try {
      const { data: { user } } = await supabase.auth.getUser();
      
      // If no user, throw a more descriptive error
      if (!user) {
        throw new Error('User not authenticated. Please sign in to delete a member.');
      }

      // Check if the user is the owner of the group that the member belongs to
      const { data: memberData, error: memberError } = await supabase
        .from('members')
        .select('group_id')
        .eq('id', id)
        .single();

      if (memberError) {
        throw new Error(`Error finding member: ${memberError.message}`);
      }

      const { data: groupData, error: groupError } = await supabase
        .from('groups')
        .select('user_id')
        .eq('id', memberData.group_id)
        .single();

      if (groupError) {
        throw new Error(`Error checking group ownership: ${groupError.message}`);
      }

      if (groupData.user_id !== user.id) {
        throw new Error('Only group owners can delete members from a group.');
      }

      const { error } = await supabase
        .from('members')
        .delete()
        .eq('id', id);
    
      if (error) throw error;
    } catch (error) {
      console.error('Error deleting member:', error);
      // Re-throw the error so it can be handled by the calling function
      throw error;
    }
  }
  
  static async findById(id: string): Promise<Member | null> {
    try {
      const { data, error } = await supabase
        .from('members')
        .select('*')
        .eq('id', id)
        .single();
      
      if (error) {
        if (error.code === 'PGRST116') {
          // No rows found
          return null;
        }
        throw error;
      }
      
      return data || null;
    } catch (error) {
      console.error('Error finding member:', error);
      // Return null instead of throwing error to prevent blank page
      return null;
    }
  }
  
  static async findByGroupId(groupId: string): Promise<Member[]> {
    try {
      const { data, error } = await supabase
        .from('members')
        .select('*')
        .eq('group_id', groupId);
      
      if (error) throw error;
      
      return data || [];
    } catch (error) {
      console.error('Error finding members by group:', error);
      // Return empty array instead of throwing error to prevent blank page
      return [];
    }
  }

  static async setAdminStatus(memberId: string, isAdmin: boolean): Promise<Member> {
    try {
      const { data: { user } } = await supabase.auth.getUser();
      
      // If no user, throw a more descriptive error
      if (!user) {
        throw new Error('User not authenticated. Please sign in to update a member.');
      }

      // Check if the user is the owner of the group that the member belongs to
      const { data: memberData, error: memberError } = await supabase
        .from('members')
        .select('group_id')
        .eq('id', memberId)
        .single();

      if (memberError) {
        throw new Error(`Error finding member: ${memberError.message}`);
      }

      const { data: groupData, error: groupError } = await supabase
        .from('groups')
        .select('user_id')
        .eq('id', memberData.group_id)
        .single();

      if (groupError) {
        throw new Error(`Error checking group ownership: ${groupError.message}`);
      }

      if (groupData.user_id !== user.id) {
        throw new Error('Only group owners can update members in a group.');
      }

      // If setting a member as admin, remove admin status from other members in the same group
      if (isAdmin) {
        // First check if the is_admin column exists
        const { data: columnCheck, error: columnError } = await supabase
          .from('members')
          .update({ is_admin: false })
          .eq('group_id', memberData.group_id)
          .neq('id', memberId)
          .select();
        
        // If there's an error about the column not existing, we'll continue without updating other members
        if (columnError && !columnError.message.includes('is_admin')) {
          throw columnError;
        }
      }

      // Update the specific member
      const { data: updatedData, error } = await supabase
        .from('members')
        .update({ is_admin: isAdmin })
        .eq('id', memberId)
        .select()
        .single();
    
      if (error) throw error;
      if (!updatedData) throw new Error('Member not found');
    
      return updatedData;
    } catch (error) {
      console.error('Error updating member admin status:', error);
      // Re-throw the error so it can be handled by the calling function
      throw error;
    }
  }

  // Helper method to create member from current user
  static async createFromCurrentUser(groupId: string): Promise<Member> {
    try {
      const { data: { user } } = await supabase.auth.getUser();
      
      // If no user, throw a more descriptive error
      if (!user) {
        throw new Error('User not authenticated. Please sign in to create a member.');
      }

      // Check if the user is the owner of the group
      const { data: groupData, error: groupError } = await supabase
        .from('groups')
        .select('user_id')
        .eq('id', groupId)
        .single();

      if (groupError) {
        throw new Error(`Error checking group ownership: ${groupError.message}`);
      }

      if (groupData.user_id !== user.id) {
        throw new Error('Only group owners can add members to a group.');
      }

      // Check if this is the first member being added to the group (should be admin)
      const { data: existingMembers, error: membersError } = await supabase
        .from('members')
        .select('id')
        .eq('group_id', groupId)
        .limit(1);

      if (membersError) {
        throw new Error(`Error checking existing members: ${membersError.message}`);
      }

      const isFirstMember = !existingMembers || existingMembers.length === 0;

      const newMember = {
        group_id: groupId,
        name: user.user_metadata?.full_name || user.email?.split('@')[0] || 'User',
        email: user.email,
        avatar_color: '#10b981',
        is_current_user: true,
        is_admin: isFirstMember,
        created_date: new Date().toISOString(),
        created_by: user.email || 'unknown',
        user_id: user.id
      };

      const { data: insertedData, error } = await supabase
        .from('members')
        .insert(newMember)
        .select()
        .single();
    
      if (error) throw error;
    
      return insertedData;
    } catch (error) {
      console.error('Error creating member from current user:', error);
      throw error;
    }
  }
}