import React, { useState } from 'react'
import { motion } from 'framer-motion'
import { Lock, Loader2, AlertCircle, CheckCircle, Eye, EyeOff } from 'lucide-react'
import { Button } from '@/components/ui/button'
import { Input } from '@/components/ui/input'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { useAuth } from '@/contexts/AuthContext'
import { useAuthOperations } from '@/hooks/useAuthOperations'

export default function ChangePassword() {
  const { user } = useAuth()
  const { handleChangePassword, loading, error: authError } = useAuthOperations()
  
  const [currentPassword, setCurrentPassword] = useState('')
  const [newPassword, setNewPassword] = useState('')
  const [confirmPassword, setConfirmPassword] = useState('')
  const [showCurrentPassword, setShowCurrentPassword] = useState(false)
  const [showNewPassword, setShowNewPassword] = useState(false)
  const [showConfirmPassword, setShowConfirmPassword] = useState(false)
  const [success, setSuccess] = useState<string | null>(null)
  const [localError, setLocalError] = useState<string | null>(null)

  const handleChangePasswordSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    
    // Clear previous messages
    setLocalError(null)
    setSuccess(null)
    
    // Validation
    if (!currentPassword || !newPassword || !confirmPassword) {
      setLocalError('Please fill in all fields')
      return
    }
    
    if (newPassword.length < 6) {
      setLocalError('New password must be at least 6 characters long')
      return
    }
    
    if (newPassword !== confirmPassword) {
      setLocalError('New passwords do not match')
      return
    }
    
    if (currentPassword === newPassword) {
      setLocalError('New password must be different from current password')
      return
    }
    
    try {
      const { success, error } = await handleChangePassword(currentPassword, newPassword)
      
      if (success) {
        setSuccess('Password updated successfully!')
        // Clear form fields
        setCurrentPassword('')
        setNewPassword('')
        setConfirmPassword('')
      } else {
        setLocalError(error || 'Failed to update password')
      }
    } catch (err) {
      console.error('Password change error:', err)
      setLocalError('An unexpected error occurred. Please try again.')
    }
  }

  // Show error from hook if there's no local error
  const displayError = localError || authError

  return (
    <div className="w-full max-w-md">
      <Card className="bg-gray-950/50 border-gray-800/50 backdrop-blur-xl shadow-2xl">
        <CardHeader className="text-center">
          <CardTitle className="text-white flex items-center justify-center gap-3">
            <Lock className="h-5 w-5 text-emerald-400" />
            Change Password
          </CardTitle>
          <CardDescription className="text-gray-400">
            Update your account password
          </CardDescription>
        </CardHeader>
        
        <CardContent className="space-y-6">
          {/* Success Message */}
          {success && (
            <motion.div
              initial={{ opacity: 0, y: -10 }}
              animate={{ opacity: 1, y: 0 }}
              className="flex items-center gap-2 p-3 border rounded-lg bg-green-500/10 border-green-500/20 text-green-400 text-sm"
            >
              <CheckCircle className="h-4 w-4 flex-shrink-0" />
              <span>{success}</span>
            </motion.div>
          )}

          {/* Error Message */}
          {displayError && (
            <motion.div
              initial={{ opacity: 0, y: -10 }}
              animate={{ opacity: 1, y: 0 }}
              className="flex items-center gap-2 p-3 border rounded-lg bg-red-500/10 border-red-500/20 text-red-400 text-sm"
            >
              <AlertCircle className="h-4 w-4 flex-shrink-0" />
              <span>{displayError}</span>
            </motion.div>
          )}

          {/* Password Change Form */}
          <form onSubmit={handleChangePasswordSubmit} className="space-y-4">
            <div className="space-y-3">
              <div className="relative">
                <Lock className="absolute left-3 top-1/2 transform -translate-y-1/2 h-4 w-4 text-gray-400" />
                <Input
                  type={showCurrentPassword ? 'text' : 'password'}
                  placeholder="Current Password"
                  value={currentPassword}
                  onChange={(e) => setCurrentPassword(e.target.value)}
                  className="pl-10 pr-10 bg-gray-800/50 border-gray-600 text-white placeholder-gray-400 focus:border-emerald-500"
                  required
                />
                <button
                  type="button"
                  onClick={() => setShowCurrentPassword(!showCurrentPassword)}
                  className="absolute right-3 top-1/2 transform -translate-y-1/2 text-gray-400 hover:text-gray-300"
                >
                  {showCurrentPassword ? <EyeOff className="h-4 w-4" /> : <Eye className="h-4 w-4" />}
                </button>
              </div>
              
              <div className="relative">
                <Lock className="absolute left-3 top-1/2 transform -translate-y-1/2 h-4 w-4 text-gray-400" />
                <Input
                  type={showNewPassword ? 'text' : 'password'}
                  placeholder="New Password"
                  value={newPassword}
                  onChange={(e) => setNewPassword(e.target.value)}
                  className="pl-10 pr-10 bg-gray-800/50 border-gray-600 text-white placeholder-gray-400 focus:border-emerald-500"
                  required
                  minLength={6}
                />
                <button
                  type="button"
                  onClick={() => setShowNewPassword(!showNewPassword)}
                  className="absolute right-3 top-1/2 transform -translate-y-1/2 text-gray-400 hover:text-gray-300"
                >
                  {showNewPassword ? <EyeOff className="h-4 w-4" /> : <Eye className="h-4 w-4" />}
                </button>
              </div>
              
              <div className="relative">
                <Lock className="absolute left-3 top-1/2 transform -translate-y-1/2 h-4 w-4 text-gray-400" />
                <Input
                  type={showConfirmPassword ? 'text' : 'password'}
                  placeholder="Confirm New Password"
                  value={confirmPassword}
                  onChange={(e) => setConfirmPassword(e.target.value)}
                  className="pl-10 pr-10 bg-gray-800/50 border-gray-600 text-white placeholder-gray-400 focus:border-emerald-500"
                  required
                />
                <button
                  type="button"
                  onClick={() => setShowConfirmPassword(!showConfirmPassword)}
                  className="absolute right-3 top-1/2 transform -translate-y-1/2 text-gray-400 hover:text-gray-300"
                >
                  {showConfirmPassword ? <EyeOff className="h-4 w-4" /> : <Eye className="h-4 w-4" />}
                </button>
              </div>
            </div>
            
            <Button
              type="submit"
              disabled={loading || !currentPassword || !newPassword || !confirmPassword}
              className="w-full bg-gradient-to-r from-emerald-500 to-blue-500 hover:from-emerald-600 hover:to-blue-600 text-white border-none shadow-lg hover:shadow-emerald-500/25 transition-all duration-300 disabled:opacity-50 disabled:cursor-not-allowed py-3 text-lg"
            >
              {loading ? (
                <motion.div
                  animate={{ rotate: 360 }}
                  transition={{ duration: 1, repeat: Infinity, ease: "linear" }}
                  className="flex items-center gap-3"
                >
                  <Loader2 className="h-5 w-5" />
                  <span>Updating Password...</span>
                </motion.div>
              ) : (
                <span>Update Password</span>
              )}
            </Button>
          </form>
        </CardContent>
      </Card>
    </div>
  );
}