import { useState } from 'react';
import { useAuth } from '@/contexts/AuthContext';
import { useAuthOperations } from '@/hooks/useAuthOperations';
import { Button } from '@/components/ui/button';
import { Card, CardContent } from '@/components/ui/card';
import { 
  LogOut, 
  Settings as SettingsIcon, 
  ChevronDown,
  User
} from 'lucide-react';
import { motion, AnimatePresence } from 'framer-motion';

export default function UserProfile() {
  const { user } = useAuth();
  const { handleSignOut, loading } = useAuthOperations();
  const [isOpen, setIsOpen] = useState(false);

  if (!user) return null;

  const userName = user.user_metadata?.full_name || user.email?.split('@')[0] || 'User';
  const userEmail = user.email || '';
  const avatarUrl = user.user_metadata?.avatar_url;

  // Dispatch a custom event to open settings in the parent component
  const handleOpenSettings = () => {
    setIsOpen(false);
    // Create and dispatch a custom event
    const event = new CustomEvent('openSettings', { detail: { tab: 'user' } });
    window.dispatchEvent(event);
  };

  return (
    <div className="relative">
      <Button
        variant="outline"
        onClick={() => setIsOpen(!isOpen)}
        className="bg-gray-800/50 border-gray-700 hover:bg-gray-700/50 hover:border-gray-600 backdrop-blur-sm p-2 h-auto"
      >
        <div className="flex items-center gap-3">
          <div className="relative">
            {avatarUrl ? (
              <img
                src={avatarUrl}
                alt={userName}
                className="w-8 h-8 rounded-full object-cover"
              />
            ) : (
              <div className="w-8 h-8 rounded-full bg-gradient-to-r from-emerald-500 to-blue-500 flex items-center justify-center text-white text-sm font-semibold">
                {userName.charAt(0).toUpperCase()}
              </div>
            )}
            <div className="absolute -bottom-1 -right-1 w-3 h-3 bg-green-500 rounded-full border-2 border-gray-900"></div>
          </div>
          
          <div className="text-left hidden sm:block">
            <div className="text-white text-sm font-medium">{userName}</div>
            <div className="text-gray-400 text-xs">{userEmail}</div>
          </div>
          
          <ChevronDown 
            className={`h-4 w-4 text-gray-400 transition-transform duration-200 ${
              isOpen ? 'rotate-180' : ''
            }`} 
          />
        </div>
      </Button>

      <AnimatePresence>
        {isOpen && (
          <>
            <div 
              className="fixed inset-0 z-10" 
              onClick={() => setIsOpen(false)}
            />
            
            <motion.div
              initial={{ opacity: 0, y: -10, scale: 0.95 }}
              animate={{ opacity: 1, y: 0, scale: 1 }}
              exit={{ opacity: 0, y: -10, scale: 0.95 }}
              transition={{ duration: 0.2 }}
              className="absolute right-0 top-full mt-2 z-20"
            >
              <Card className="bg-gray-900/95 border-gray-700/50 backdrop-blur-xl shadow-2xl min-w-[280px]">
                <CardContent className="p-0">
                  <div className="p-4 border-b border-gray-700/50">
                    <div className="flex items-center gap-3">
                      {avatarUrl ? (
                        <img
                          src={avatarUrl}
                          alt={userName}
                          className="w-12 h-12 rounded-full object-cover"
                        />
                      ) : (
                        <div className="w-12 h-12 rounded-full bg-gradient-to-r from-emerald-500 to-blue-500 flex items-center justify-center text-white text-lg font-semibold">
                          {userName.charAt(0).toUpperCase()}
                        </div>
                      )}
                      <div>
                        <div className="text-white font-semibold">{userName}</div>
                        <div className="text-gray-400 text-sm">{userEmail}</div>
                        <div className="text-emerald-400 text-xs mt-1">● Online</div>
                      </div>
                    </div>
                  </div>

                  <div className="p-2">
                    <Button
                      variant="ghost"
                      className="w-full justify-start text-gray-300 hover:text-white hover:bg-gray-700/50 mb-1"
                      onClick={handleOpenSettings}
                    >
                      <User className="h-4 w-4 mr-3" />
                      Profile
                    </Button>
                    
                    <Button
                      variant="ghost"
                      className="w-full justify-start text-gray-300 hover:text-white hover:bg-gray-700/50 mb-1"
                      onClick={handleOpenSettings}
                    >
                      <SettingsIcon className="h-4 w-4 mr-3" />
                      Account Settings
                    </Button>
                    
                    <Button
                      variant="ghost"
                      className="w-full justify-start text-red-400 hover:text-red-300 hover:bg-red-500/10"
                      onClick={() => {
                        setIsOpen(false);
                        handleSignOut();
                      }}
                      disabled={loading}
                    >
                      <LogOut className="h-4 w-4 mr-3" />
                      {loading ? 'Signing out...' : 'Sign Out'}
                    </Button>
                  </div>
                </CardContent>
              </Card>
            </motion.div>
          </>
        )}
      </AnimatePresence>
    </div>
  );
}