import { useState } from 'react';
import { Member } from '@/entities/Member';
import { formatCurrency } from '@/utils/currency';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Badge } from '@/components/ui/badge';
import { 
  Users, 
  Plus, 
  Trash2, 
  Edit2, 
  Mail, 
  Phone, 
  Save,
  X,
  UserPlus,
  Crown,
  Shield
} from 'lucide-react';
import { motion, AnimatePresence } from 'framer-motion';

export default function MembersManager({ 
  groupId, 
  members, 
  balances, 
  group,
  onMembersUpdated 
}: any) {
  const [newMember, setNewMember] = useState({
    name: '',
    email: '',
    phone: '',
    avatar_color: '#10b981'
  });
  const [editingMember, setEditingMember] = useState<any>(null);
  const [loading, setLoading] = useState(false);
  const [showAddForm, setShowAddForm] = useState(false);

  const avatarColors = [
    '#10b981', '#3b82f6', '#8b5cf6', '#f59e0b', 
    '#ef4444', '#ec4899', '#06b6d4', '#84cc16',
    '#f97316', '#6366f1', '#14b8a6', '#eab308'
  ];

  const handleAddMember = async (e: React.FormEvent) => {
    e.preventDefault();
    e.stopPropagation(); // Stop event from bubbling up
    
    // Additional validation
    if (!newMember.name.trim()) {
      alert('Please enter a name for the member');
      return;
    }

    setLoading(true);
    try {
      await Member.create({
        group_id: groupId,
        name: newMember.name.trim(),
        email: newMember.email.trim(),
        phone: newMember.phone.trim(),
        avatar_color: newMember.avatar_color,
        is_current_user: false
      });

      setNewMember({
        name: '',
        email: '',
        phone: '',
        avatar_color: '#10b981'
      });
      setShowAddForm(false);
      await onMembersUpdated();
    } catch (error: any) {
      console.error('Error adding member:', error);
      // Show error message to user
      alert(`Failed to add member: ${error.message || 'Unknown error'}`);
    } finally {
      setLoading(false);
    }
  };

  // Add a separate function to handle button clicks to ensure proper event handling
  const handleAddMemberClick = async (e: React.MouseEvent) => {
    e.preventDefault();
    const fakeEvent = {
      preventDefault: () => {},
      stopPropagation: () => {}
    } as React.FormEvent;
    await handleAddMember(fakeEvent);
  };

  const handleUpdateMember = async (memberId: string, updates: any) => {
    setLoading(true);
    try {
      await Member.update(memberId, updates);
      setEditingMember(null);
      await onMembersUpdated();
    } catch (error) {
      console.error('Error updating member:', error);
    }
    setLoading(false);
  };

  const handleDeleteMember = async (memberId: string) => {
    if (window.confirm('Are you sure you want to remove this member? This will affect expense calculations.')) {
      setLoading(true);
      try {
        await Member.delete(memberId);
        await onMembersUpdated();
      } catch (error) {
        console.error('Error deleting member:', error);
      }
      setLoading(false);
    }
  };

  const handleSetAdmin = async (memberId: string) => {
    setLoading(true);
    try {
      // First, check if this member is already admin
      const member = members.find((m: any) => m.id === memberId);
      if (member && member.is_admin) {
        // If already admin, don't do anything
        return;
      }
      
      // Set this member as admin (this will automatically remove admin status from others)
      await Member.setAdminStatus(memberId, true);
      await onMembersUpdated();
    } catch (error) {
      console.error('Error setting admin status:', error);
      // Check if it's a column error
      if ((error as Error).message.includes('is_admin')) {
        alert('Admin feature is not available yet. Please run the database migration to enable this feature.');
      } else {
        alert('Failed to set admin status: ' + (error as Error).message);
      }
    }
    setLoading(false);
  };

  const getBalanceStatus = (balance: number) => {
    if (Math.abs(balance) < 0.01) return { text: 'Settled', color: 'text-green-400', bg: 'bg-green-500/20' };
    if (balance > 0) return { text: `Gets ${formatCurrency(balance, group?.currency || 'USD', 'symbol')}`, color: 'text-green-400', bg: 'bg-green-500/20' };
    return { text: `Owes ${formatCurrency(Math.abs(balance), group?.currency || 'USD', 'symbol')}`, color: 'text-red-400', bg: 'bg-red-500/20' };
  };

  return (
    <div className="space-y-6">
      {/* Header with Add Button */}
      <div className="flex items-center justify-between">
        <div>
          <h2 className="text-2xl font-bold text-white">Group Members</h2>
          <p className="text-gray-400">Manage who's in this group</p>
        </div>
        <Button 
          onClick={() => setShowAddForm(!showAddForm)}
          className="bg-gradient-to-r from-emerald-500 to-blue-500 hover:from-emerald-600 hover:to-blue-600"
        >
          <UserPlus className="h-4 w-4 mr-2" />
          Add Member
        </Button>
      </div>

      {/* Add Member Form */}
      <AnimatePresence>
        {showAddForm && (
          <motion.div
            initial={{ opacity: 0, height: 0 }}
            animate={{ opacity: 1, height: 'auto' }}
            exit={{ opacity: 0, height: 0 }}
            className="overflow-hidden"
          >
            <Card className="bg-gray-950/50 border-gray-800/50 backdrop-blur-xl">
              <CardHeader>
                <CardTitle className="text-white flex items-center gap-2">
                  <Plus className="h-5 w-5 text-emerald-400" />
                  Add New Member
                </CardTitle>
              </CardHeader>
              <CardContent>
                <div className="space-y-4">
                  <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                    <div>
                      <Label className="text-gray-400">Name *</Label>
                      <Input
                        value={newMember.name}
                        onChange={(e) => setNewMember(prev => ({ ...prev, name: e.target.value }))}
                        placeholder="Full name"
                        className="bg-gray-800/50 border-gray-700 text-white mt-1"
                        required
                      />
                    </div>
                    <div>
                      <Label className="text-gray-400">Email</Label>
                      <div className="relative mt-1">
                        <Mail className="absolute left-3 top-1/2 -translate-y-1/2 h-4 w-4 text-gray-400" />
                        <Input
                          type="email"
                          value={newMember.email}
                          onChange={(e) => setNewMember(prev => ({ ...prev, email: e.target.value }))}
                          placeholder="email@example.com"
                          className="bg-gray-800/50 border-gray-700 text-white pl-10"
                        />
                      </div>
                    </div>
                    <div>
                      <Label className="text-gray-400">Phone</Label>
                      <div className="relative mt-1">
                        <Phone className="absolute left-3 top-1/2 -translate-y-1/2 h-4 w-4 text-gray-400" />
                        <Input
                          type="tel"
                          value={newMember.phone}
                          onChange={(e) => setNewMember(prev => ({ ...prev, phone: e.target.value }))}
                          placeholder="+1 (555) 000-0000"
                          className="bg-gray-800/50 border-gray-700 text-white pl-10"
                        />
                      </div>
                    </div>
                    <div>
                      <Label className="text-gray-400">Avatar Color</Label>
                      <div className="flex flex-wrap gap-2 mt-2">
                        {avatarColors.map(color => (
                          <button
                            key={color}
                            type="button"
                            onClick={() => setNewMember(prev => ({ ...prev, avatar_color: color }))}
                            className={`w-8 h-8 rounded-full border-2 transition-all ${
                              newMember.avatar_color === color ? 'border-white scale-110' : 'border-gray-600'
                            }`}
                            style={{ backgroundColor: color }}
                          />
                        ))}
                      </div>
                    </div>
                  </div>
                  <div className="flex gap-2 pt-4">
                    <Button 
                      onClick={async (e) => {
                        e.preventDefault();
                        e.stopPropagation();
                        
                        if (!newMember.name.trim()) {
                          alert('Please enter a name for the member');
                          return;
                        }

                        setLoading(true);
                        try {
                          await Member.create({
                            group_id: groupId,
                            name: newMember.name.trim(),
                            email: newMember.email.trim(),
                            phone: newMember.phone.trim(),
                            avatar_color: newMember.avatar_color,
                            is_current_user: false
                          });

                          setNewMember({
                            name: '',
                            email: '',
                            phone: '',
                            avatar_color: '#10b981'
                          });
                          setShowAddForm(false);
                          await onMembersUpdated();
                        } catch (error: any) {
                          console.error('Error adding member:', error);
                          alert(`Failed to add member: ${error.message || 'Unknown error'}`);
                        } finally {
                          setLoading(false);
                        }
                      }}
                      disabled={loading || !newMember.name.trim()}
                    >
                      {loading ? 'Adding...' : 'Add Member'}
                    </Button>
                    <Button type="button" variant="outline" onClick={() => setShowAddForm(false)}>
                      Cancel
                    </Button>
                  </div>
                </div>
              </CardContent>
            </Card>
          </motion.div>
        )}
      </AnimatePresence>

      {/* Members List */}
      <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
        <AnimatePresence>
          {members.map((member: any, index: number) => {
            const balance = balances[member.id] || 0;
            const balanceStatus = getBalanceStatus(balance);
            const isEditing = editingMember?.id === member.id;

            return (
              <motion.div
                key={member.id}
                initial={{ opacity: 0, y: 20 }}
                animate={{ opacity: 1, y: 0 }}
                exit={{ opacity: 0, y: -20 }}
                transition={{ delay: index * 0.1 }}
                className="group"
              >
                <Card className="bg-gradient-to-br from-gray-900/90 to-gray-800/90 border-gray-700/50 backdrop-blur-xl shadow-xl hover:shadow-lg transition-all duration-300">
                  <CardContent className="p-6">
                    {isEditing ? (
                      <div className="space-y-4">
                        <div>
                          <Label className="text-gray-400">Name</Label>
                          <Input
                            value={editingMember.name}
                            onChange={(e) => setEditingMember((prev: any) => ({ ...prev, name: e.target.value }))}
                            className="bg-gray-800/50 border-gray-700 text-white mt-1"
                          />
                        </div>
                        <div>
                          <Label className="text-gray-400">Email</Label>
                          <Input
                            type="email"
                            value={editingMember.email}
                            onChange={(e) => setEditingMember((prev: any) => ({ ...prev, email: e.target.value }))}
                            className="bg-gray-800/50 border-gray-700 text-white mt-1"
                          />
                        </div>
                        <div>
                          <Label className="text-gray-400">Phone</Label>
                          <Input
                            type="tel"
                            value={editingMember.phone}
                            onChange={(e) => setEditingMember((prev: any) => ({ ...prev, phone: e.target.value }))}
                            className="bg-gray-800/50 border-gray-700 text-white mt-1"
                          />
                        </div>
                        <div>
                          <Label className="text-gray-400">Avatar Color</Label>
                          <div className="flex flex-wrap gap-2 mt-2">
                            {avatarColors.map(color => (
                              <button
                                key={color}
                                type="button"
                                onClick={() => setEditingMember((prev: any) => ({ ...prev, avatar_color: color }))}
                                className={`w-6 h-6 rounded-full border-2 transition-all ${
                                  editingMember.avatar_color === color ? 'border-white scale-110' : 'border-gray-600'
                                }`}
                                style={{ backgroundColor: color }}
                              />
                            ))}
                          </div>
                        </div>
                        <div className="flex gap-2">
                          <Button 
                            size="sm" 
                            onClick={() => handleUpdateMember(member.id, editingMember)}
                            disabled={loading}
                          >
                            <Save className="h-3 w-3 mr-1" />
                            Save
                          </Button>
                          <Button 
                            size="sm" 
                            variant="outline" 
                            onClick={() => setEditingMember(null)}
                          >
                            <X className="h-3 w-3 mr-1" />
                            Cancel
                          </Button>
                        </div>
                      </div>
                    ) : (
                      <div className="space-y-4">
                        <div className="flex items-start justify-between">
                          <div className="flex items-center gap-3">
                            <div 
                              className="w-12 h-12 rounded-full flex items-center justify-center text-lg font-bold text-black shadow-lg"
                              style={{ backgroundColor: member.avatar_color }}
                            >
                              {member.name.charAt(0).toUpperCase()}
                            </div>
                            <div>
                              <div className="flex items-center gap-2">
                                <h3 className="text-white font-semibold text-lg">{member.name}</h3>
                                {member.is_current_user && (
                                  <Badge className="bg-blue-500/20 text-blue-400 border-blue-500/30 text-xs">
                                    <Crown className="h-3 w-3 mr-1" />
                                    You
                                  </Badge>
                                )}
                                {member.is_admin && (
                                  <Badge className="bg-purple-500/20 text-purple-400 border-purple-500/30 text-xs">
                                    <Shield className="h-3 w-3 mr-1" />
                                    Admin
                                  </Badge>
                                )}
                              </div>
                              {member.email && (
                                <p className="text-gray-400 text-sm flex items-center gap-1">
                                  <Mail className="h-3 w-3" />
                                  {member.email}
                                </p>
                              )}
                              {member.phone && (
                                <p className="text-gray-400 text-sm flex items-center gap-1">
                                  <Phone className="h-3 w-3" />
                                  {member.phone}
                                </p>
                              )}
                            </div>
                          </div>
                          <div className="flex gap-1 opacity-0 group-hover:opacity-100 transition-opacity">
                            {!member.is_admin && (
                              <Button
                                size="icon"
                                variant="ghost"
                                onClick={() => handleSetAdmin(member.id)}
                                className="h-8 w-8 text-purple-400 hover:text-purple-300 hover:bg-purple-500/10"
                                title="Set as Admin"
                              >
                                <Shield className="h-3 w-3" />
                              </Button>
                            )}
                            <Button
                              size="icon"
                              variant="ghost"
                              onClick={() => setEditingMember({ ...member })}
                              className="h-8 w-8 text-gray-400 hover:text-white"
                            >
                              <Edit2 className="h-3 w-3" />
                            </Button>
                            {!member.is_current_user && (
                              <Button
                                size="icon"
                                variant="ghost"
                                onClick={() => handleDeleteMember(member.id)}
                                className="h-8 w-8 text-red-400 hover:text-red-300 hover:bg-red-500/10"
                              >
                                <Trash2 className="h-3 w-3" />
                              </Button>
                            )}
                          </div>
                        </div>

                        {/* Balance Status */}
                        <div className={`p-3 rounded-lg ${balanceStatus.bg} border border-current/20`}>
                          <div className="flex items-center justify-between">
                            <span className="text-gray-300 text-sm">Balance Status</span>
                            <span className={`font-semibold ${balanceStatus.color}`}>
                              {balanceStatus.text}
                            </span>
                          </div>
                        </div>
                      </div>
                    )}
                  </CardContent>
                </Card>
              </motion.div>
            );
          })}
        </AnimatePresence>
      </div>

      {members.length === 0 && (
        <Card className="bg-gray-950/50 border-gray-800/50 backdrop-blur-xl">
          <CardContent className="pt-6">
            <div className="text-center py-12">
              <Users className="h-12 w-12 text-gray-400 mx-auto mb-4" />
              <h3 className="text-white text-lg font-medium mb-2">No members yet</h3>
              <p className="text-gray-400 mb-6">
                Add members to start tracking shared expenses
              </p>
              <Button 
                onClick={() => setShowAddForm(true)}
                className="bg-gradient-to-r from-emerald-500 to-blue-500 hover:from-emerald-600 hover:to-blue-600"
              >
                <Plus className="h-4 w-4 mr-2" />
                Add First Member
              </Button>
            </div>
          </CardContent>
        </Card>
      )}
    </div>
  );
}