import { motion } from 'framer-motion';
import { Split, Code } from 'lucide-react';

export default function SplashScreen() {
  return (
    <div className="h-screen w-screen flex flex-col items-center justify-center bg-gradient-to-b from-teal-900 via-gray-900 to-purple-900 relative overflow-hidden">
      {/* Background Gradients */}
      <div className="absolute top-[-20%] left-[-20%] w-[50%] h-[50%] bg-teal-500/20 rounded-full blur-3xl animate-pulse"></div>
      <div className="absolute bottom-[-20%] right-[-20%] w-[50%] h-[50%] bg-purple-600/20 rounded-full blur-3xl animate-pulse delay-1000"></div>

      <motion.div
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 1, ease: 'easeOut' }}
        className="flex flex-col items-center justify-center text-center z-10 p-8"
      >
        {/* Logo */}
        <motion.div
          className="relative mb-8"
          initial={{ scale: 0 }}
          animate={{ scale: 1 }}
          transition={{ delay: 0.3, type: "spring", stiffness: 150 }}
        >
          <div className="absolute -inset-4 bg-teal-400/30 rounded-full blur-2xl animate-pulse"></div>
          <div className="relative w-24 h-24 bg-gray-800/80 rounded-full flex items-center justify-center border-2 border-teal-400/50 backdrop-blur-sm">
            <Split className="w-12 h-12 text-yellow-400" />
          </div>
        </motion.div>

        {/* Text Content */}
        <motion.h1
          className="text-6xl font-bold text-white mb-4 tracking-tight"
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ delay: 0.8, duration: 0.8 }}
        >
          SplitMate
        </motion.h1>
        <motion.p
          className="text-xl text-gray-300"
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          transition={{ delay: 1.2, duration: 0.8 }}
        >
          Split expenses with friends effortlessly
        </motion.p>
      </motion.div>

      {/* Footer */}
      <motion.div
        className="absolute bottom-8"
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ delay: 1.5, duration: 1 }}
      >
        <div className="bg-black/50 backdrop-blur-sm rounded-full px-6 py-3 flex items-center gap-3 text-sm text-gray-300 border border-white/10">
          <Code className="w-4 h-4" />
          <span>Created by md.raihanulislam.x@gmail.com</span>
        </div>
      </motion.div>
    </div>
  );
}