import { useState } from 'react';
import { motion } from 'framer-motion';
import { Plus, Users, Sparkles, Archive, RotateCcw } from 'lucide-react';
import { formatCurrency } from '@/utils/currency';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Input } from '@/components/ui/input';
import { Badge } from '@/components/ui/badge';
import { Group } from '@/entities/Group';
import CurrencySelector from '@/components/ui/currency-selector';
import { Label } from '@/components/ui/label';
import { Loader2 } from 'lucide-react';

interface GroupSelectorProps {
  groups: any[];
  currentUser?: any;
  onGroupSelect: (groupId: string) => void;
  onGroupCreate: () => void;
}

export default function GroupSelector({ 
  groups, 
  currentUser,
  onGroupSelect,
  onGroupCreate
}: GroupSelectorProps) {
  const [showCreateForm, setShowCreateForm] = useState(false);
  const [newGroupName, setNewGroupName] = useState('');
  const [newGroupBudget, setNewGroupBudget] = useState('');
  const [newGroupCurrency, setNewGroupCurrency] = useState('USD');
  const [loading, setLoading] = useState(false);
  const [showArchived, setShowArchived] = useState(false);
  const [error, setError] = useState<string | null>(null);

  const handleCreateGroup = async () => {
    if (!newGroupName.trim()) return;
    
    console.log('Attempting to create group with data:', {
      name: newGroupName.trim(),
      budget: parseFloat(newGroupBudget) || 0,
      currency: newGroupCurrency
    });
    
    setLoading(true);
    setError(null);
    try {
      const newGroup = await Group.create({
        name: newGroupName.trim(),
        budget: parseFloat(newGroupBudget) || 0,
        currency: newGroupCurrency as any, // Type assertion to bypass strict typing
        completed: false,
        archived: false
      });
      
      console.log('Group created successfully:', newGroup);
      
      setNewGroupName('');
      setNewGroupBudget('');
      setNewGroupCurrency('USD');
      setShowCreateForm(false);
      onGroupCreate();
      onGroupSelect(newGroup.id);
    } catch (error: any) {
      console.error('Error creating group:', error);
      setError(error.message || 'Failed to create group. Please try again.');
    }
    setLoading(false);
  };

  const handleRestoreGroup = async (groupId: string) => {
    try {
      await Group.update(groupId, { archived: false });
      onGroupCreate(); // Refresh groups
    } catch (error) {
      console.error('Error restoring group:', error);
    }
  };

  const activeGroups = groups.filter(g => !g.archived);
  const archivedGroups = groups.filter(g => g.archived);

  return (
    <div className="min-h-screen bg-black flex items-center justify-center p-4">
      <div className="fixed inset-0 overflow-hidden pointer-events-none">
        <div className="absolute top-20 left-10 w-72 h-72 bg-emerald-500/3 rounded-full blur-3xl"></div>
        <div className="absolute top-40 right-10 w-96 h-96 bg-blue-500/3 rounded-full blur-3xl"></div>
        <div className="absolute bottom-20 left-1/3 w-80 h-80 bg-purple-500/3 rounded-full blur-3xl"></div>
      </div>
      
      <motion.div 
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        className="w-full max-w-4xl relative z-10"
      >
        <div className="text-center mb-8">
          <motion.h1 
            initial={{ opacity: 0, y: -20 }}
            animate={{ opacity: 1, y: 0 }}
            className="text-5xl font-bold bg-gradient-to-r from-emerald-400 to-cyan-400 bg-clip-text text-transparent mb-4"
          >
            SplitMate
          </motion.h1>
          <motion.p 
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            transition={{ delay: 0.2 }}
            className="text-gray-400 text-lg"
          >
            Choose a group or create a new one to get started
          </motion.p>
        </div>

        {/* Error Message */}
        {error && (
          <motion.div
            initial={{ opacity: 0, y: -10 }}
            animate={{ opacity: 1, y: 0 }}
            className="mb-6 p-4 bg-red-500/10 border border-red-500/20 rounded-lg text-red-400 text-center"
          >
            {error}
          </motion.div>
        )}

        {/* Tab Buttons */}
        <div className="flex justify-center mb-6">
          <div className="flex bg-gray-900/50 rounded-lg p-1">
            <Button
              onClick={() => setShowArchived(false)}
              variant={!showArchived ? "default" : "ghost"}
              className={!showArchived ? "bg-emerald-500 text-white" : "text-gray-400 hover:text-white"}
            >
              <Users className="h-4 w-4 mr-2" />
              Active Groups ({activeGroups.length})
            </Button>
            <Button
              onClick={() => setShowArchived(true)}
              variant={showArchived ? "default" : "ghost"}
              className={showArchived ? "bg-emerald-500 text-white" : "text-gray-400 hover:text-white"}
            >
              <Archive className="h-4 w-4 mr-2" />
              Archived ({archivedGroups.length})
            </Button>
          </div>
        </div>

        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6 mb-8">
          {(!showArchived ? activeGroups : archivedGroups).map((group, index) => (
            <motion.div
              key={group.id}
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ delay: 0.1 * index }}
            >
              <Card 
                className="bg-gray-950/50 border-gray-800/50 backdrop-blur-xl hover:border-gray-700/50 transition-all duration-300 cursor-pointer group relative"
                onClick={() => showArchived ? null : onGroupSelect(group.id)}
              >
                {showArchived && (
                  <div className="absolute top-2 right-2">
                    <Badge variant="secondary" className="bg-yellow-500/20 text-yellow-400 border-yellow-500/30">
                      <Archive className="h-3 w-3 mr-1" />
                      Archived
                    </Badge>
                  </div>
                )}
                <CardHeader>
                  <CardTitle className="text-white flex items-center gap-3">
                    <div className="p-2 bg-gradient-to-r from-emerald-500/20 to-blue-500/20 rounded-lg">
                      <Users className="h-5 w-5 text-emerald-400" />
                    </div>
                    {group.name}
                  </CardTitle>
                </CardHeader>
                <CardContent>
                  <div className="space-y-2 text-sm text-gray-400">
                    <div className="flex justify-between">
                      <span>Budget:</span>
                      <span className="text-emerald-400">{formatCurrency(group.budget || 0, group.currency || 'USD', currentUser?.currency_display || 'symbol')}</span>
                    </div>
                    <div className="flex justify-between">
                      <span>Created:</span>
                      <span>{new Date(group.created_date).toLocaleDateString()}</span>
                    </div>
                  </div>
                  {showArchived && (
                    <div className="mt-4 flex gap-2">
                      <Button
                        onClick={(e) => {
                          e.stopPropagation();
                          handleRestoreGroup(group.id);
                        }}
                        size="sm"
                        className="flex-1 bg-emerald-500/20 text-emerald-400 border-emerald-500/30 hover:bg-emerald-500/30"
                        variant="outline"
                      >
                        <RotateCcw className="h-3 w-3 mr-1" />
                        Restore
                      </Button>
                    </div>
                  )}
                </CardContent>
              </Card>
            </motion.div>
          ))}
          
          {(!showArchived ? activeGroups : archivedGroups).length === 0 && (
            <div className="col-span-full text-center py-12">
              <motion.div
                initial={{ opacity: 0 }}
                animate={{ opacity: 1 }}
                className="text-gray-400"
              >
                {showArchived ? (
                  <>
                    <Archive className="h-12 w-12 mx-auto mb-4 opacity-50" />
                    <p>No archived groups found</p>
                  </>
                ) : (
                  <>
                    <Users className="h-12 w-12 mx-auto mb-4 opacity-50" />
                    <p>No active groups found. Create your first group to get started!</p>
                  </>
                )}
              </motion.div>
            </div>
          )}
        </div>

        <motion.div
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          transition={{ delay: 0.4 }}
          className="text-center"
        >
          {!showArchived && (
            !showCreateForm ? (
              <Button
                onClick={() => setShowCreateForm(true)}
                className="bg-gradient-to-r from-emerald-500 to-blue-500 hover:from-emerald-600 hover:to-blue-600 text-white px-8 py-3 text-lg"
              >
                <Plus className="h-5 w-5 mr-2" />
                Create New Group
              </Button>
            ) : (
              <Card className="bg-gray-950/50 border-gray-800/50 backdrop-blur-xl max-w-md mx-auto">
                <CardHeader>
                  <CardTitle className="text-white flex items-center gap-3">
                    <Sparkles className="h-5 w-5 text-emerald-400" />
                    Create New Group
                  </CardTitle>
                </CardHeader>
                <CardContent className="space-y-4">
                  <div className="space-y-2">
                    <Label htmlFor="group-name" className="text-gray-300">Group Name</Label>
                    <Input
                      id="group-name"
                      value={newGroupName}
                      onChange={(e) => setNewGroupName(e.target.value)}
                      placeholder="e.g., Trip to Mexico"
                      className="bg-gray-800 border-gray-700 text-white"
                    />
                  </div>
                  
                  <div className="grid grid-cols-2 gap-4">
                    <div className="space-y-2">
                      <Label htmlFor="group-budget" className="text-gray-300">Budget</Label>
                      <Input
                        id="group-budget"
                        type="number"
                        value={newGroupBudget}
                        onChange={(e) => setNewGroupBudget(e.target.value)}
                        placeholder="0.00"
                        className="bg-gray-800 border-gray-700 text-white"
                      />
                    </div>
                    
                    <div className="space-y-2">
                      <Label htmlFor="group-currency" className="text-gray-300">Currency</Label>
                      <CurrencySelector
                        value={newGroupCurrency}
                        onValueChange={setNewGroupCurrency}
                        className="bg-gray-800 border-gray-700 text-white"
                      />
                    </div>
                  </div>
                  
                  <div className="flex gap-3 pt-2">
                    <Button
                      onClick={handleCreateGroup}
                      disabled={!newGroupName.trim() || loading}
                      className="flex-1 bg-gradient-to-r from-emerald-500 to-blue-500 hover:from-emerald-600 hover:to-blue-600 text-white"
                    >
                      {loading ? (
                        <>
                          <Loader2 className="mr-2 h-4 w-4 animate-spin" />
                          Creating...
                        </>
                      ) : (
                        <>
                          <Plus className="mr-2 h-4 w-4" />
                          Create Group
                        </>
                      )}
                    </Button>
                    <Button
                      onClick={() => {
                        setShowCreateForm(false);
                        setNewGroupName('');
                        setNewGroupBudget('');
                        setNewGroupCurrency('USD');
                        setError(null);
                      }}
                      variant="outline"
                      className="border-gray-700 text-gray-300 hover:bg-gray-800"
                    >
                      Cancel
                    </Button>
                  </div>
                </CardContent>
              </Card>
            )
          )}
        </motion.div>
      </motion.div>
    </div>
  );
}