// Expense Entity - TypeScript interface and Supabase CRUD operations

import { supabase } from '@/lib/supabase'

export interface Expense {
  id: string;
  group_id: string;
  description: string;
  amount: number;
  currency: string;
  category: 'Food' | 'Transport' | 'Accommodation' | 'Activities' | 'Shopping' | 'Other';
  is_personal: boolean;
  paid_by_group_budget: boolean;
  paid_by_member_ids?: string[];
  participant_member_ids: string[];
  payer_amounts?: { [memberId: string]: number };
  location?: {
    lat: number;
    lng: number;
    address: string;
    place_name: string;
  };
  receipt_image_url?: string;
  notes?: string;
  created_date: string;
  created_by: string;
}

// CRUD operations with Supabase
export class Expense {
  static async list(sort?: string): Promise<Expense[]> {
    try {
      // Add a timeout to prevent hanging
      const timeoutPromise = new Promise((_, reject) => 
        setTimeout(() => reject(new Error('Expenses loading timeout')), 3000)
      );
      
      const dataPromise = (async () => {
        try {
          let query = supabase
            .from('expenses')
            .select('*');

          if (sort === '-created_date') {
            query = query.order('created_date', { ascending: false });
          }
          
          const { data, error } = await query;
          
          if (error) {
            console.warn('Expenses query error:', error.message);
            return [];
          }
          
          return data || [];
        } catch (error) {
          console.warn('Expenses data fetch error:', error);
          return [];
        }
      })();
      
      const result = await Promise.race([dataPromise, timeoutPromise]);
      return result as Expense[];
    } catch (error) {
      console.warn('Error fetching expenses (returning empty array):', error);
      // Return empty array instead of throwing error to prevent blank page
      return [];
    }
  }
  
  static async create(data: Omit<Expense, 'id' | 'created_date' | 'created_by'>): Promise<Expense> {
    try {
      // Validate required fields
      if (!data.group_id) {
        throw new Error('group_id is required');
      }
      if (!data.description) {
        throw new Error('description is required');
      }
      if (typeof data.amount !== 'number' || isNaN(data.amount) || data.amount <= 0) {
        throw new Error('valid amount is required');
      }
      if (!data.currency) {
        throw new Error('currency is required');
      }
      if (!data.category) {
        throw new Error('category is required');
      }
      
      // Get current user
      const { data: { user } } = await supabase.auth.getUser();
      
      if (!user) {
        throw new Error('User not authenticated. Please sign in to create an expense.');
      }
      
      // Check if the user is the owner of the group
      const { data: groupData, error: groupError } = await supabase
        .from('groups')
        .select('user_id')
        .eq('id', data.group_id)
        .single();

      if (groupError) {
        throw new Error(`Error checking group ownership: ${groupError.message}`);
      }

      if (groupData.user_id !== user.id) {
        throw new Error('Only group owners can add expenses to a group.');
      }
      
      // If this is a personal expense, assign it to the admin member if one exists
      let processedData = { ...data };
      if (data.is_personal && (!data.paid_by_member_ids || data.paid_by_member_ids.length === 0)) {
        // Find the admin member for this group
        const { data: adminMember, error: memberError } = await supabase
          .from('members')
          .select('id')
          .eq('group_id', data.group_id)
          .eq('is_admin', true)
          .single();
        
        if (!memberError && adminMember) {
          processedData.paid_by_member_ids = [adminMember.id];
        }
      }
      
      const newExpense = {
        ...processedData,
        created_date: new Date().toISOString(),
        created_by: user.email || 'unknown'
      };

      const { data: insertedData, error } = await supabase
        .from('expenses')
        .insert(newExpense)
        .select()
        .single();
      
      if (error) throw error;
      
      return insertedData;
    } catch (error) {
      console.error('Error creating expense:', error);
      // Re-throw the error so it can be handled by the calling function
      throw error;
    }
  }
  
  static async update(id: string, data: Partial<Expense>): Promise<Expense> {
    try {
      const { data: { user } } = await supabase.auth.getUser();
      
      if (!user) {
        throw new Error('User not authenticated. Please sign in to update an expense.');
      }
      
      // Check if the user is the owner of the group that the expense belongs to
      const { data: expenseData, error: expenseError } = await supabase
        .from('expenses')
        .select('group_id')
        .eq('id', id)
        .single();

      if (expenseError) {
        throw new Error(`Error finding expense: ${expenseError.message}`);
      }

      const { data: groupData, error: groupError } = await supabase
        .from('groups')
        .select('user_id')
        .eq('id', expenseData.group_id)
        .single();

      if (groupError) {
        throw new Error(`Error checking group ownership: ${groupError.message}`);
      }

      if (groupData.user_id !== user.id) {
        throw new Error('Only group owners can update expenses in a group.');
      }

      const { data: updatedData, error } = await supabase
        .from('expenses')
        .update(data)
        .eq('id', id)
        .select()
        .single();
    
      if (error) throw error;
      if (!updatedData) throw new Error('Expense not found');
      
      return updatedData;
    } catch (error) {
      console.error('Error updating expense:', error);
      // Re-throw the error so it can be handled by the calling function
      throw error;
    }
  }
  
  static async delete(id: string): Promise<void> {
    try {
      const { data: { user } } = await supabase.auth.getUser();
      
      if (!user) {
        throw new Error('User not authenticated. Please sign in to delete an expense.');
      }
      
      // Check if the user is the owner of the group that the expense belongs to
      const { data: expenseData, error: expenseError } = await supabase
        .from('expenses')
        .select('group_id')
        .eq('id', id)
        .single();

      if (expenseError) {
        throw new Error(`Error finding expense: ${expenseError.message}`);
      }

      const { data: groupData, error: groupError } = await supabase
        .from('groups')
        .select('user_id')
        .eq('id', expenseData.group_id)
        .single();

      if (groupError) {
        throw new Error(`Error checking group ownership: ${groupError.message}`);
      }

      if (groupData.user_id !== user.id) {
        throw new Error('Only group owners can delete expenses from a group.');
      }

      const { error } = await supabase
        .from('expenses')
        .delete()
        .eq('id', id);
    
      if (error) throw error;
    } catch (error) {
      console.error('Error deleting expense:', error);
      // Re-throw the error so it can be handled by the calling function
      throw error;
    }
  }
  
  static async findById(id: string): Promise<Expense | null> {
    try {
      const { data, error } = await supabase
        .from('expenses')
        .select('*')
        .eq('id', id)
        .single();
      
      if (error) {
        if (error.code === 'PGRST116') {
          // No rows found
          return null;
        }
        throw error;
      }
      
      return data || null;
    } catch (error) {
      console.error('Error finding expense:', error);
      // Return null instead of throwing error to prevent blank page
      return null;
    }
  }
  
  static async findByGroupId(groupId: string): Promise<Expense[]> {
    try {
      const { data, error } = await supabase
        .from('expenses')
        .select('*')
        .eq('group_id', groupId);
      
      if (error) throw error;
      
      return data || [];
    } catch (error) {
      console.error('Error finding expenses by group:', error);
      // Return empty array instead of throwing error to prevent blank page
      return [];
    }
  }
}