export type CurrencyDisplayType = 'symbol' | 'code' | 'name';

export interface Currency {
  code: string;
  name: string;
  symbol: string;
  flag: string;
}

export function getCurrency(currencyCode: string): Currency | undefined {
  return CURRENCIES.find(c => c.code === currencyCode);
}

export function formatCurrency(
  amount: number,
  currencyCode: string = 'USD',
  displayType: CurrencyDisplayType = 'symbol'
): string {
  const currency = getCurrency(currencyCode);
  
  if (!currency) {
    // Instead of hardcoded $, use a more generic fallback
    return `${amount.toFixed(2)} ${currencyCode}`;
  }

  const formattedAmount = amount.toFixed(2);

  switch (displayType) {
    case 'symbol':
      return `${currency.symbol}${formattedAmount}`;
    case 'code':
      return `${currency.code} ${formattedAmount}`;
    case 'name':
      return `${formattedAmount} ${currency.name}`;
    default:
      return `${currency.symbol}${formattedAmount}`;
  }
}

export function getCurrencySymbol(currencyCode: string): string {
  const currency = getCurrency(currencyCode);
  return currency?.symbol || '$';
}

export function getCurrencyName(currencyCode: string): string {
  const currency = getCurrency(currencyCode);
  return currency?.name || 'US Dollar';
}

export function getCurrencyFlag(currencyCode: string): string {
  const currency = getCurrency(currencyCode);
  return currency?.flag || '🇺🇸';
}

export { CURRENCIES };