import { useState, useEffect, useMemo } from 'react';
import { Group } from '@/entities/Group';
import { Member } from '@/entities/Member';
import { Expense } from '@/entities/Expense';
import { Button } from '@/components/ui/button';
import { Card, CardContent } from '@/components/ui/card';
import { Badge } from '@/components/ui/badge';
import { Tabs, TabsList, TabsTrigger, TabsContent } from '@/components/ui/tabs';
import { formatCurrency } from '@/utils/currency';
import { 
  Plus, 
  Users, 
  PieChart, 
  Receipt,
  TrendingUp,
  Settings,
  Sparkles,
  Utensils,
  Car,
  Home,
  PartyPopper,
  ShoppingBag,
  MoreHorizontal,
  AlertCircle
} from 'lucide-react';
import { motion, AnimatePresence } from 'framer-motion';

import SplashScreen from '../components/splitmate/SplashScreen';
import GroupSelector from '../components/splitmate/GroupSelector';
import UserProfile from '../components/auth/UserProfile';
import GroupDashboard from '../components/splitmate/GroupDashboard';
import ExpenseForm from '../components/splitmate/ExpenseForm';
import ExpensesList from '../components/splitmate/ExpensesList';
import MembersManager from '../components/splitmate/MembersManager';
import Analytics from '../components/splitmate/Analytics';
import SettingsPanel from '../components/splitmate/SettingsPanel';

const CATEGORIES = [
  { name: 'Food', icon: Utensils, color: '#ff6b6b', gradient: 'from-red-500 to-orange-500' },
  { name: 'Transport', icon: Car, color: '#4ecdc4', gradient: 'from-blue-500 to-cyan-500' },
  { name: 'Accommodation', icon: Home, color: '#45b7d1', gradient: 'from-indigo-500 to-blue-500' },
  { name: 'Activities', icon: PartyPopper, color: '#f9ca24', gradient: 'from-yellow-500 to-orange-500' },
  { name: 'Shopping', icon: ShoppingBag, color: '#6c5ce7', gradient: 'from-purple-500 to-pink-500' },
  { name: 'Other', icon: MoreHorizontal, color: '#a0a0a0', gradient: 'from-gray-500 to-slate-500' },
];

export default function SplitMate() {
  const [showSplash, setShowSplash] = useState(true);
  const [groups, setGroups] = useState<any[]>([]);
  const [members, setMembers] = useState<any[]>([]);
  const [expenses, setExpenses] = useState<any[]>([]);
  const [currentGroupId, setCurrentGroupId] = useState<string | null>(null);
  const [activeTab, setActiveTab] = useState('dashboard');
  const [loading, setLoading] = useState(true);
  const [showSettings, setShowSettings] = useState(false);
  const [settingsTab, setSettingsTab] = useState('group'); // Track which settings tab to show
  const [error, setError] = useState<string | null>(null);
  
  // Mock user data - in real app this would come from authentication context
  const [currentUser, setCurrentUser] = useState({
    id: 'user-1',
    email: 'user@example.com',
    display_name: 'Current User',
    email_notifications: true,
    push_notifications: true,
    theme: 'dark',
    currency_display: 'symbol',
    privacy_mode: false
  });

  useEffect(() => {
    const timer = setTimeout(() => {
      setShowSplash(false);
    }, 3000);
    
    // Add a failsafe timeout
    const failsafeTimer = setTimeout(() => {
      if (showSplash) {
        console.log('Splash screen failsafe timeout reached');
        setShowSplash(false);
      }
    }, 5000);
    
    return () => {
      clearTimeout(timer);
      clearTimeout(failsafeTimer);
    };
  }, []);

  // Add event listener for opening settings from UserProfile
  useEffect(() => {
    const handleOpenSettings = (event: CustomEvent) => {
      const tab = event.detail?.tab || 'group';
      setSettingsTab(tab);
      setShowSettings(true);
    };

    window.addEventListener('openSettings', handleOpenSettings as EventListener);
    return () => {
      window.removeEventListener('openSettings', handleOpenSettings as EventListener);
    };
  }, []);

  useEffect(() => {
    if (!showSplash) {
      loadData();
    }
  }, [showSplash]);

  const loadData = async (selectGroupId: string | null = null) => {
    console.log('loadData called with selectGroupId:', selectGroupId);
    setLoading(true);
    setError(null);
    
    // Add a timeout to prevent infinite loading
    const timeoutId = setTimeout(() => {
      if (loading) {
        console.log('Load data timeout reached, using mock data');
        setGroups([]);
        setMembers([]);
        setExpenses([]);
        setLoading(false);
      }
    }, 5000);
    
    try {
      console.log('Loading data from entities...');
      
      // Add timeouts to each data loading operation
      const groupsPromise = Promise.race([
        Group.list('-created_date'),
        new Promise(resolve => setTimeout(() => resolve([]), 3000))
      ]);
      
      const membersPromise = Promise.race([
        Member.list('-created_date'),
        new Promise(resolve => setTimeout(() => resolve([]), 3000))
      ]);
      
      const expensesPromise = Promise.race([
        Expense.list('-created_date'),
        new Promise(resolve => setTimeout(() => resolve([]), 3000))
      ]);
      
      const [groupsData, membersData, expensesData] = await Promise.all([
        groupsPromise,
        membersPromise,
        expensesPromise
      ]) as [any[], any[], any[]];
      
      console.log('Data loaded:', { groups: groupsData.length, members: membersData.length, expenses: expensesData.length });
      
      const activeGroups = groupsData.filter(g => !g.archived);
      setGroups(groupsData);
      setMembers(membersData);
      setExpenses(expensesData);
      
      if (selectGroupId) {
        setCurrentGroupId(selectGroupId);
      } else if (!currentGroupId && activeGroups.length > 0) {
        setCurrentGroupId(activeGroups[0].id);
      } else if (currentGroupId && !groupsData.find(g => g.id === currentGroupId)) {
        setCurrentGroupId(activeGroups.length > 0 ? activeGroups[0].id : null);
      }
      
      console.log('State updated successfully');
    } catch (err) {
      console.warn('Error loading data (continuing with empty data):', err);
      // Continue with empty data instead of showing error
      setGroups([]);
      setMembers([]);
      setExpenses([]);
    } finally {
      clearTimeout(timeoutId);
      setLoading(false);
    }
    console.log('loadData completed');
  };
  
  const handleGroupDeleted = () => {
    console.log('handleGroupDeleted called');
    loadData();
    setCurrentGroupId(null);
    console.log('handleGroupDeleted completed');
  };
  
  const handleGroupUpdated = async () => {
    await loadData(currentGroupId);
  };

  // Settings panel callback functions
  const handleUserPreferencesUpdate = async (updates: any) => {
    try {
      console.log('Updating user preferences:', updates);
      // Update local user state
      setCurrentUser(prev => ({ ...prev, ...updates }));
      // In real app, this would update user preferences in database
      alert('User preferences updated successfully!');
    } catch (err) {
      console.error('Error updating user preferences:', err);
      alert('Failed to update user preferences');
    }
  };

  const handleDataExport = async (format: string) => {
    try {
      console.log('Exporting data in format:', format);
      setLoading(true);
      setError(null);
      
      // Simulate export delay
      await new Promise(resolve => setTimeout(resolve, 1500));
      
      if (format === 'csv') {
        // Generate CSV data
        const csvData = generateCSVData(currentExpenses, currentMembers);
        downloadFile(csvData, `${currentGroup?.name}-expenses.csv`, 'text/csv');
      } else if (format === 'json') {
        // Generate JSON data
        const jsonData = JSON.stringify({
          group: currentGroup,
          members: currentMembers,
          expenses: currentExpenses,
          exported_at: new Date().toISOString()
        }, null, 2);
        downloadFile(jsonData, `${currentGroup?.name}-data.json`, 'application/json');
      } else if (format === 'pdf') {
        // In real app, this would generate a PDF report
        alert('PDF export functionality would be implemented here');
      }
      
      alert(`Data exported successfully as ${format.toUpperCase()}!`);
    } catch (err) {
      console.error('Error exporting data:', err);
      setError('Failed to export data. Please try again.');
      alert('Failed to export data');
    } finally {
      setLoading(false);
    }
  };

  // Helper function to generate CSV data
  const generateCSVData = (expenses: any[], members: any[]) => {
    const headers = ['Date', 'Description', 'Amount', 'Category', 'Paid By', 'Participants'];
    const rows = expenses.map(expense => {
      const paidBy = expense.paid_by_group_budget ? 'Group Budget' : 
        Object.keys(expense.payer_amounts || {}).map(id => 
          members.find(m => m.id === id)?.name || 'Unknown'
        ).join(', ');
      
      const participants = (expense.participant_member_ids || []).map((id: string) => 
        members.find(m => m.id === id)?.name || 'Unknown'
      ).join(', ');
      
      return [
        new Date(expense.created_date).toLocaleDateString(),
        expense.description,
        expense.amount.toFixed(2),
        expense.category,
        paidBy,
        participants
      ];
    });
    
    return [headers, ...rows].map(row => row.join(',')).join('\n');
  };

  // Helper function to download file
  const downloadFile = (content: string, filename: string, mimeType: string) => {
    const blob = new Blob([content], { type: mimeType });
    const url = URL.createObjectURL(blob);
    const link = document.createElement('a');
    link.href = url;
    link.download = filename;
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
    URL.revokeObjectURL(url);
  };

  const currentGroup = useMemo(() => groups.find(g => g.id === currentGroupId), [groups, currentGroupId]);
  const currentMembers = useMemo(() => members.filter(m => m.group_id === currentGroupId), [members, currentGroupId]);
  const currentExpenses = useMemo(() => expenses.filter(e => e.group_id === currentGroupId), [expenses, currentGroupId]);

  const balances = useMemo(() => {
    if (!currentGroup || currentMembers.length === 0) return {};
    
    const bal = currentMembers.reduce((acc, m) => ({ ...acc, [m.id]: 0 }), {});
    
    currentExpenses.forEach(expense => {
      // Skip personal expenses as they don't affect group balances
      if (expense.is_personal) return;
      
      if (expense.paid_by_group_budget) {
        // Group Budget Expense: Handle participants vs non-participants
        if (expense.participant_member_ids?.length > 0) {
          // Some members participated, others didn't
          const shareAmount = expense.amount / expense.participant_member_ids.length;
          const totalMembers = currentMembers.length;
          const participantCount = expense.participant_member_ids.length;
          const nonParticipantCount = totalMembers - participantCount;
          
          if (nonParticipantCount > 0) {
            // Non-participants: They paid via group budget but didn't benefit
            // They should get compensated for their share of the group payment
            const groupPaymentPerMember = expense.amount / totalMembers;
            
            currentMembers.forEach(member => {
              if (bal[member.id] !== undefined) {
                const isParticipant = expense.participant_member_ids.includes(member.id);
                
                if (isParticipant) {
                  // Participants: They benefited but also paid via group budget
                  bal[member.id] += groupPaymentPerMember; // Paid via group budget
                  bal[member.id] -= shareAmount; // Owe their share of benefit
                } else {
                  // Non-participants: They paid via group budget but got no benefit
                  bal[member.id] += groupPaymentPerMember; // Paid via group budget
                  // They don't owe anything since they didn't participate
                }
              }
            });
          } else {
            // All members participated - everyone paid equally and benefited equally
            const shareAmount = expense.amount / currentMembers.length;
            currentMembers.forEach(member => {
              if (bal[member.id] !== undefined) {
                bal[member.id] += shareAmount; // Paid via group budget
                bal[member.id] -= shareAmount; // Owe their share
                // Net effect: 0 balance for everyone
              }
            });
          }
        } else {
          // No specific participants defined - treat as all members participating
          const shareAmount = expense.amount / currentMembers.length;
          currentMembers.forEach(member => {
            if (bal[member.id] !== undefined) {
              bal[member.id] += shareAmount; // Paid via group budget
              bal[member.id] -= shareAmount; // Owe their share
              // Net effect: 0 balance for everyone
            }
          });
        }
      } else {
        // Custom Payers & Participants: Calculate based on who paid vs who participated
        
        // Add amounts for payers (they get credited for what they paid)
        if (expense.payer_amounts) {
          Object.entries(expense.payer_amounts).forEach(([memberId, amount]) => {
            if (bal[memberId] !== undefined) {
              bal[memberId] += amount; // Payer gets positive balance
            }
          });
        }
        
        // Subtract share amounts for participants (they owe their share)
        if (expense.participant_member_ids?.length > 0) {
          const shareAmount = expense.amount / expense.participant_member_ids.length;
          expense.participant_member_ids.forEach((memberId: string) => {
            if (bal[memberId] !== undefined) {
              bal[memberId] -= shareAmount; // Participants owe their share
            }
          });
        }
      }
    });
    
    return bal;
  }, [currentGroup, currentMembers, currentExpenses]);

  const totalGroupSpending = useMemo(() => {
    return currentExpenses
      .filter(e => e.paid_by_group_budget)
      .reduce((sum, e) => sum + e.amount, 0);
  }, [currentExpenses]);

  const remainingBudget = (currentGroup?.budget || 0) - totalGroupSpending;

  if (showSplash) {
    return <SplashScreen />;
  }

  if (loading && !error) {
    return (
      <div className="min-h-screen bg-black flex items-center justify-center">
        <motion.div 
          className="relative"
          initial={{ opacity: 0, scale: 0.8 }}
          animate={{ opacity: 1, scale: 1 }}
          transition={{ duration: 0.5 }}
        >
          <motion.div 
            animate={{ rotate: 360 }}
            transition={{ duration: 2, repeat: Infinity, ease: "linear" }}
            className="w-16 h-16 border-4 border-emerald-500/20 border-t-emerald-500 rounded-full"
          />
          <div className="absolute inset-0 flex items-center justify-center">
            <Sparkles className="w-6 h-6 text-emerald-400" />
          </div>
          <p className="text-emerald-400 text-sm mt-4 text-center font-medium">Loading SplitMate...</p>
        </motion.div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="min-h-screen bg-black flex items-center justify-center p-4">
        <Card className="bg-gray-950/50 border-gray-800/50 backdrop-blur-xl max-w-md w-full">
          <CardContent className="pt-6 text-center">
            <div className="text-red-400 mb-4">
              <AlertCircle className="w-12 h-12 mx-auto" />
            </div>
            <h2 className="text-xl font-bold text-white mb-2">Error Loading Data</h2>
            <p className="text-gray-400 mb-6">{error}</p>
            <Button 
              onClick={() => {
                setError(null);
                loadData();
              }}
              className="bg-gradient-to-r from-emerald-500 to-blue-500 hover:from-emerald-600 hover:to-blue-600 text-white"
            >
              Try Again
            </Button>
          </CardContent>
        </Card>
      </div>
    );
  }

  if (!currentGroupId) {
    return (
      <GroupSelector 
        groups={groups}
        currentUser={currentUser}
        onGroupSelect={setCurrentGroupId}
        onGroupCreate={() => loadData()}
      />
    );
  }

  return (
    <div className="min-h-screen bg-black">
      <div className="fixed inset-0 overflow-hidden pointer-events-none">
        <div className="absolute top-20 left-10 w-72 h-72 bg-emerald-500/3 rounded-full blur-3xl"></div>
        <div className="absolute top-40 right-10 w-96 h-96 bg-blue-500/3 rounded-full blur-3xl"></div>
        <div className="absolute bottom-20 left-1/3 w-80 h-80 bg-purple-500/3 rounded-full blur-3xl"></div>
      </div>
      <div className="relative z-10 container mx-auto px-4 py-6">
        <motion.div initial={{ opacity: 0, y: -20 }} animate={{ opacity: 1, y: 0 }} className="mb-8">
          <div className="flex items-center justify-between mb-6">
            <div>
              <motion.button 
                onClick={() => setCurrentGroupId(null)} 
                className="group flex items-center gap-2 text-emerald-400 hover:text-emerald-300 text-sm mb-3 transition-all duration-300 hover:gap-3" 
                whileHover={{ x: -4 }} 
                whileTap={{ scale: 0.98 }}
              >
                <span>←</span>
                <span className="font-medium">Back to Groups</span>
              </motion.button>
              <motion.h1 
                className="text-4xl font-bold bg-gradient-to-r from-emerald-400 to-cyan-400 bg-clip-text text-transparent mb-2" 
                initial={{ opacity: 0, x: -20 }} 
                animate={{ opacity: 1, x: 0 }} 
                transition={{ delay: 0.1 }}
              >
                {currentGroup?.name}
              </motion.h1>
              <motion.div 
                className="flex items-center gap-4 text-gray-400" 
                initial={{ opacity: 0, x: -20 }} 
                animate={{ opacity: 1, x: 0 }} 
                transition={{ delay: 0.2 }}
              >
                <span className="flex items-center gap-1">
                  <Receipt className="w-4 h-4" />
                  {currentExpenses.length} expenses
                </span>
                <span className="flex items-center gap-1">
                  <Users className="w-4 h-4" />
                  {currentMembers.length} members
                </span>
                {currentGroup?.completed && (
                  <Badge className="bg-emerald-500/20 text-emerald-400 border-emerald-500/30">
                    Completed
                  </Badge>
                )}
              </motion.div>
            </div>
            <div className="flex gap-3">
              <UserProfile />
              <motion.div whileHover={{ scale: 1.05 }} whileTap={{ scale: 0.95 }}>
                <Button 
                  onClick={() => {
                    setSettingsTab('user');
                    setShowSettings(true);
                  }} 
                  variant="outline" 
                  size="icon" 
                  className="bg-gray-800/50 border-gray-700 hover:bg-gray-700/50 hover:border-gray-600 backdrop-blur-sm"
                >
                  <Settings className="h-4 w-4 text-gray-300" />
                </Button>
              </motion.div>
            </div>
          </div>

          <motion.div 
            initial={{ opacity: 0, y: 20 }} 
            animate={{ opacity: 1, y: 0 }} 
            transition={{ delay: 0.3 }}
          >
            <Card className="bg-gradient-to-r from-gray-900/90 to-gray-800/90 border-gray-700/50 backdrop-blur-xl shadow-2xl">
              <CardContent className="pt-6">
                <div className="grid grid-cols-1 md:grid-cols-3 gap-6 mb-6">
                  <motion.div className="text-center md:text-left" whileHover={{ scale: 1.02 }}>
                    <p className="text-gray-400 text-sm font-medium mb-1">Total Budget</p>
                    <p className="text-3xl font-bold bg-gradient-to-r from-blue-400 to-cyan-400 bg-clip-text text-transparent">
                      {formatCurrency(currentGroup?.budget || 0, currentGroup?.currency || 'USD', currentUser.currency_display as 'symbol' | 'code' | 'name')}
                    </p>
                  </motion.div>
                  <motion.div className="text-center md:text-left" whileHover={{ scale: 1.02 }}>
                    <p className="text-gray-400 text-sm font-medium mb-1">Total Spent</p>
                    <p className="text-3xl font-bold bg-gradient-to-r from-emerald-400 to-green-400 bg-clip-text text-transparent">
                      {formatCurrency(totalGroupSpending, currentGroup?.currency || 'USD', currentUser.currency_display as 'symbol' | 'code' | 'name')}
                    </p>
                  </motion.div>
                  <motion.div className="text-center md:text-left" whileHover={{ scale: 1.02 }}>
                    <p className="text-gray-400 text-sm font-medium mb-1">
                      {remainingBudget >= 0 ? 'Remaining' : 'Over Budget'}
                    </p>
                    <p className={`text-3xl font-bold bg-gradient-to-r ${remainingBudget >= 0 ? 'from-green-400 to-emerald-400' : 'from-red-400 to-orange-400'} bg-clip-text text-transparent`}>
                      {formatCurrency(Math.abs(remainingBudget), currentGroup?.currency || 'USD', currentUser.currency_display as 'symbol' | 'code' | 'name')}
                    </p>
                  </motion.div>
                </div>
                <div className="relative">
                  <div className="bg-gray-800 rounded-full h-3 overflow-hidden">
                    <motion.div 
                      className="h-full bg-gradient-to-r from-emerald-500 via-blue-500 to-purple-500 rounded-full relative" 
                      style={{ width: `${Math.min(100, (totalGroupSpending / Math.max(currentGroup?.budget || 1, 1)) * 100)}%`}} 
                      initial={{ width: 0 }} 
                      animate={{ width: `${Math.min(100, (totalGroupSpending / Math.max(currentGroup?.budget || 1, 1)) * 100)}%`}} 
                      transition={{ duration: 1.5, ease: "easeOut" }}
                    >
                      <div className="absolute inset-0 bg-gradient-to-r from-white/20 to-transparent rounded-full"></div>
                    </motion.div>
                  </div>
                  <div className="flex justify-between text-xs text-gray-500 mt-2">
                    <span>{formatCurrency(0, currentGroup?.currency || 'USD', currentUser.currency_display as 'symbol' | 'code' | 'name')}</span>
                    <span className="font-medium">
                      {((totalGroupSpending / Math.max(currentGroup?.budget || 1, 1)) * 100).toFixed(1)}% used
                    </span>
                    <span>{formatCurrency(currentGroup?.budget || 0, currentGroup?.currency || 'USD', currentUser.currency_display as 'symbol' | 'code' | 'name')}</span>
                  </div>
                </div>
              </CardContent>
            </Card>
          </motion.div>
        </motion.div>

        <motion.div 
          initial={{ opacity: 0, y: 20 }} 
          animate={{ opacity: 1, y: 0 }} 
          transition={{ delay: 0.4 }}
        >
          <Tabs value={activeTab} onValueChange={setActiveTab} className="mb-8">
            <TabsList className="bg-gray-900/80 border-gray-700/50 backdrop-blur-xl p-1 rounded-xl shadow-2xl">
              {[
                { value: 'dashboard', icon: TrendingUp, label: 'Dashboard' },
                { value: 'expenses', icon: Receipt, label: 'Expenses' },
                { value: 'members', icon: Users, label: 'Members' },
                { value: 'analytics', icon: PieChart, label: 'Analytics' }
              ].map((tab) => (
                <TabsTrigger 
                  key={tab.value} 
                  value={tab.value} 
                  className="flex items-center gap-2 data-[state=active]:bg-gradient-to-r data-[state=active]:from-emerald-500/20 data-[state=active]:to-blue-500/20 data-[state=active]:text-emerald-400 text-gray-400 hover:text-gray-200 transition-all duration-300 rounded-lg px-4 py-2"
                >
                  <tab.icon className="h-4 w-4" />
                  <span className="font-medium">{tab.label}</span>
                </TabsTrigger>
              ))}
            </TabsList>
            <AnimatePresence mode="wait">
              <motion.div 
                key={activeTab} 
                initial={{ opacity: 0, x: 20 }} 
                animate={{ opacity: 1, x: 0 }} 
                exit={{ opacity: 0, x: -20 }} 
                transition={{ duration: 0.3 }} 
                className="mt-8"
              >
                <TabsContent value="dashboard">
                  <GroupDashboard 
                    group={currentGroup} 
                    members={currentMembers} 
                    expenses={currentExpenses} 
                    balances={balances} 
                    categories={CATEGORIES} 
                    currentUser={currentUser}
                    onRefresh={loadData}
                  />
                </TabsContent>
                <TabsContent value="expenses">
                  <div className="grid grid-cols-1 lg:grid-cols-2 gap-8">
                    <ExpenseForm 
                      groupId={currentGroupId} 
                      members={currentMembers} 
                      categories={CATEGORIES} 
                      group={currentGroup}
                      onExpenseAdded={loadData}
                    />
                    <ExpensesList 
                      expenses={currentExpenses} 
                      members={currentMembers} 
                      categories={CATEGORIES} 
                      group={currentGroup}
                      currentUser={currentUser}
                      onExpenseDeleted={loadData}
                    />
                  </div>
                </TabsContent>
                <TabsContent value="members">
                  <MembersManager 
                    groupId={currentGroupId} 
                    members={currentMembers} 
                    balances={balances} 
                    group={currentGroup}
                    currentUser={currentUser}
                    onMembersUpdated={loadData}
                  />
                </TabsContent>
                <TabsContent value="analytics">
                  <Analytics 
                    expenses={currentExpenses} 
                    members={currentMembers} 
                    categories={CATEGORIES} 
                    group={currentGroup}
                  />
                </TabsContent>
              </motion.div>
            </AnimatePresence>
          </Tabs>
        </motion.div>
        
        {showSettings && (
          <div className="fixed inset-0 bg-black/50 flex items-center justify-center z-50 p-4" onClick={() => setShowSettings(false)}>
            <div className="bg-gray-900 rounded-lg max-w-4xl w-full max-h-[90vh] overflow-y-auto" onClick={(e) => e.stopPropagation()}>
              <div className="p-6">
                <div className="flex items-center justify-between mb-6">
                  <h2 className="text-2xl font-bold text-white">Settings</h2>
                  <Button 
                    onClick={() => setShowSettings(false)}
                    variant="outline" 
                    size="icon" 
                    className="bg-gray-800/50 border-gray-700 hover:bg-gray-700/50"
                  >
                    ×
                  </Button>
                </div>
                <SettingsPanel 
                  group={currentGroup} 
                  user={currentUser} 
                  expenses={currentExpenses}
                  members={currentMembers}
                  settingsTab={settingsTab} // Pass the settingsTab prop
                  onGroupUpdate={async (updates) => {
                    console.log('Group update:', updates);
                    await handleGroupUpdated();
                  }}
                  onUserUpdate={handleUserPreferencesUpdate}
                  onExportData={handleDataExport}
                  onDeleteGroup={async () => {
                    console.log('onDeleteGroup callback called');
                    await handleGroupDeleted();
                    setShowSettings(false);
                    console.log('onDeleteGroup callback completed');
                  }}
                />
              </div>
            </div>
          </div>
        )}

        <motion.button 
          initial={{ scale: 0, rotate: -180 }} 
          animate={{ scale: 1, rotate: 0 }} 
          transition={{ delay: 0.8, type: "spring", stiffness: 200 }} 
          whileHover={{ scale: 1.1, rotate: 5 }} 
          whileTap={{ scale: 0.95 }} 
          className="fixed bottom-8 right-8 bg-gradient-to-r from-emerald-500 to-blue-500 text-white rounded-full p-4 shadow-2xl hover:shadow-emerald-500/25 transition-all duration-300 z-50 group" 
          onClick={() => setActiveTab('expenses')}
        >
          <Plus className="h-6 w-6 group-hover:rotate-90 transition-transform duration-300" />
          <div className="absolute inset-0 bg-gradient-to-r from-emerald-600 to-blue-600 rounded-full blur opacity-0 group-hover:opacity-50 transition-opacity duration-300 -z-10"></div>
        </motion.button>
      </div>
    </div>
  );
}