import React from 'react';
import { motion } from 'framer-motion';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { CheckCircle, AlertCircle } from 'lucide-react';

interface ReceiptPreviewProps {
  data: {
    description: string;
    amount: number;
    category: string;
    date?: string | null;
    items?: string[];
    paymentMethod?: string | null;
  };
  onConfirm: () => void;
  onReject: () => void;
}

export default function ReceiptPreview({ data, onConfirm, onReject }: ReceiptPreviewProps) {
  return (
    <motion.div
      initial={{ opacity: 0, y: 20 }}
      animate={{ opacity: 1, y: 0 }}
      exit={{ opacity: 0, y: -20 }}
      className="mb-4"
    >
      <Card className="bg-gray-950/50 border-emerald-500/30 backdrop-blur-xl shadow-2xl">
        <CardHeader>
          <CardTitle className="text-white flex items-center gap-3 text-lg">
            <div className="p-2 bg-emerald-500/20 rounded-lg">
              <CheckCircle className="h-5 w-5 text-emerald-400" />
            </div>
            Receipt Details Preview
          </CardTitle>
        </CardHeader>
        <CardContent className="space-y-4">
          <div className="grid grid-cols-2 gap-4">
            <div>
              <p className="text-gray-400 text-sm">Description</p>
              <p className="text-white font-medium">{data.description}</p>
            </div>
            <div>
              <p className="text-gray-400 text-sm">Amount</p>
              <p className="text-white font-medium">${data.amount.toFixed(2)}</p>
            </div>
            <div>
              <p className="text-gray-400 text-sm">Category</p>
              <p className="text-white font-medium">{data.category}</p>
            </div>
            {data.date && (
              <div>
                <p className="text-gray-400 text-sm">Date</p>
                <p className="text-white font-medium">{data.date}</p>
              </div>
            )}
            {data.paymentMethod && (
              <div>
                <p className="text-gray-400 text-sm">Payment Method</p>
                <p className="text-white font-medium">{data.paymentMethod}</p>
              </div>
            )}
          </div>
          
          {data.items && data.items.length > 0 && (
            <div>
              <p className="text-gray-400 text-sm mb-1">Items</p>
              <div className="flex flex-wrap gap-2">
                {data.items.map((item, index) => (
                  <span key={index} className="bg-emerald-500/20 text-emerald-300 px-2 py-1 rounded text-xs">
                    {item}
                  </span>
                ))}
              </div>
            </div>
          )}
          
          <div className="flex gap-2 pt-2">
            <Button 
              onClick={onConfirm}
              className="flex-1 bg-emerald-600 hover:bg-emerald-700 text-white"
            >
              <CheckCircle className="h-4 w-4 mr-2" />
              Use These Details
            </Button>
            <Button 
              onClick={onReject}
              variant="outline"
              className="flex-1 border-gray-600 text-gray-300 hover:bg-gray-800"
            >
              <AlertCircle className="h-4 w-4 mr-2" />
              Scan Again
            </Button>
          </div>
          
          <p className="text-gray-500 text-xs text-center">
            You can edit these details after confirming
          </p>
        </CardContent>
      </Card>
    </motion.div>
  );
}