// Group Entity - TypeScript interface and Supabase CRUD operations

import { supabase } from '@/lib/supabase'

export interface Group {
  id: string;
  name: string;
  description?: string;
  budget: number;
  currency: 'USD' | 'EUR' | 'GBP' | 'CAD' | 'AUD' | 'JPY' | 'CHF' | 'CNY' | 'SEK' | 'NZD' | 'MXN' | 'SGD' | 'HKD' | 'NOK' | 'KRW' | 'TRY' | 'RUB' | 'INR' | 'BRL' | 'ZAR' | 'DKK' | 'PLN' | 'TWD' | 'BGN' | 'CZK' | 'HUF' | 'RON' | 'HRK' | 'ISK' | 'PHP' | 'MYR' | 'THB' | 'IDR' | 'CLP' | 'PEN' | 'COP' | 'ARS' | 'UYU' | 'ILS' | 'AED' | 'SAR' | 'QAR' | 'KWD' | 'BHD' | 'OMR' | 'JOD' | 'LBP' | 'EGP' | 'MAD' | 'TND' | 'DZD' | 'NGN' | 'KES' | 'GHS' | 'ETB' | 'UGX' | 'TZS' | 'RWF' | 'ZMW' | 'BWP' | 'MUR' | 'SCR' | 'PKR' | 'BDT' | 'LKR' | 'NPR' | 'MVR' | 'AFN' | 'BTC' | 'ETH';
  default_split_method?: 'equal' | 'percentage' | 'custom';
  completed: boolean;
  archived: boolean;
  trip_start_location?: {
    lat: number;
    lng: number;
    address: string;
  };
  trip_end_location?: {
    lat: number;
    lng: number;
    address: string;
  };
  created_date: string;
  created_by: string;
  user_id: string;
}

// CRUD operations with Supabase
export class Group {
  static async list(sort?: string): Promise<Group[]> {
    try {
      // Add a timeout to prevent hanging
      const timeoutPromise = new Promise((_, reject) => 
        setTimeout(() => reject(new Error('Groups loading timeout')), 3000)
      );
      
      const dataPromise = (async () => {
        try {
          // Get current user
          const { data: { user }, error: authError } = await supabase.auth.getUser();
          
          // If there's an auth error, return empty array instead of throwing error
          if (authError) {
            console.warn('Authentication error:', authError.message);
            return [];
          }
          
          // If no user, return empty array instead of throwing error
          if (!user) {
            console.warn('No authenticated user found, returning empty groups list');
            return [];
          }

          console.log('Fetching groups for user:', user.id);

          // Query groups for the current user
          let query = supabase
            .from('groups')
            .select('*')
            .eq('user_id', user.id);

          if (sort === '-created_date') {
            query = query.order('created_date', { ascending: false });
          }
          
          const { data, error } = await query;
          
          if (error) {
            console.warn('Groups query error:', error.message);
            return [];
          }
          
          console.log('Groups fetched:', data?.length || 0);
          return data || [];
        } catch (error) {
          console.warn('Groups data fetch error:', error);
          return [];
        }
      })();
      
      const result = await Promise.race([dataPromise, timeoutPromise]);
      return result as Group[];
    } catch (error) {
      console.warn('Error fetching groups (returning empty array):', error);
      // Return empty array instead of throwing error to prevent blank page
      return [];
    }
  }
  
  static async create(data: Omit<Group, 'id' | 'created_date' | 'created_by' | 'user_id'>): Promise<Group> {
    try {
      // Get current user
      const { data: { user }, error: authError } = await supabase.auth.getUser();
      
      // If there's an auth error, throw a more descriptive error
      if (authError) {
        console.error('Authentication error when creating group:', authError.message);
        throw new Error(`Authentication error: ${authError.message}`);
      }
      
      // If no user, throw a more descriptive error
      if (!user) {
        console.error('No authenticated user when creating group');
        throw new Error('User not authenticated. Please sign in to create a group.');
      }

      console.log('Creating group for user:', user.id, 'with data:', data);

      const newGroup = {
        ...data,
        created_date: new Date().toISOString(),
        created_by: user.email || 'unknown',
        user_id: user.id,
        description: data.description || '',
        default_split_method: data.default_split_method || 'equal'
      };

      const { data: insertedData, error } = await supabase
        .from('groups')
        .insert(newGroup)
        .select()
        .single();
      
      if (error) {
        console.error('Error inserting group:', error);
        throw error;
      }
      
      console.log('Group created successfully:', insertedData);
      return insertedData;
    } catch (error) {
      console.error('Error creating group:', error);
      // Re-throw the error so it can be handled by the calling function
      throw error;
    }
  }
  
  static async update(id: string, data: Partial<Group>): Promise<Group> {
    try {
      // Get current user
      const { data: { user }, error: authError } = await supabase.auth.getUser();
      
      // If there's an auth error, throw a more descriptive error
      if (authError) {
        throw new Error(`Authentication error: ${authError.message}`);
      }
      
      // If no user, throw a more descriptive error
      if (!user) {
        throw new Error('User not authenticated. Please sign in to update a group.');
      }

      const { data: updatedData, error } = await supabase
        .from('groups')
        .update(data)
        .eq('id', id)
        .eq('user_id', user.id)
        .select()
        .single();
      
      if (error) throw error;
      if (!updatedData) throw new Error('Group not found or access denied');
      
      return updatedData;
    } catch (error) {
      console.error('Error updating group:', error);
      // Re-throw the error so it can be handled by the calling function
      throw error;
    }
  }
  
  static async delete(id: string): Promise<void> {
    try {
      console.log('Group.delete called with ID:', id);
      
      // Get current user
      const { data: { user }, error: authError } = await supabase.auth.getUser();
      console.log('User authentication check:', { user: !!user, authError: !!authError });
      
      // If there's an auth error, throw a more descriptive error
      if (authError) {
        console.error('Authentication error in Group.delete:', authError.message);
        throw new Error(`Authentication error: ${authError.message}`);
      }
      
      // If no user, throw a more descriptive error
      if (!user) {
        console.error('No authenticated user in Group.delete');
        throw new Error('User not authenticated. Please sign in to delete a group.');
      }

      // First, check if the group exists and belongs to the user
      console.log('Checking if group exists for user:', user.id);
      const { data: existingGroup, error: findError } = await supabase
        .from('groups')
        .select('id, name, user_id')
        .eq('id', id)
        .eq('user_id', user.id)
        .single();
      
      if (findError) {
        console.error('Error finding group:', findError);
        throw new Error(`Group not found or access denied: ${findError.message}`);
      }
      
      if (!existingGroup) {
        console.error('Group not found for user');
        throw new Error('Group not found or access denied');
      }
      
      console.log('Group found:', existingGroup);

      // Delete all related expenses first
      console.log('Deleting related expenses for group:', id);
      const { error: expensesError } = await supabase
        .from('expenses')
        .delete()
        .eq('group_id', id);
      
      if (expensesError) {
        console.error('Error deleting expenses:', expensesError);
        throw new Error(`Failed to delete group expenses: ${expensesError.message}`);
      }
      console.log('Expenses deleted successfully');

      // Delete all related members
      console.log('Deleting related members for group:', id);
      const { error: membersError } = await supabase
        .from('members')
        .delete()
        .eq('group_id', id);
      
      if (membersError) {
        console.error('Error deleting members:', membersError);
        throw new Error(`Failed to delete group members: ${membersError.message}`);
      }
      console.log('Members deleted successfully');

      // Now delete the group itself
      console.log('Attempting to delete group with ID:', id, 'for user:', user.id);
      
      const { error } = await supabase
        .from('groups')
        .delete()
        .eq('id', id)
        .eq('user_id', user.id);
      
      if (error) {
        console.error('Supabase delete error:', error);
        throw error;
      }
      
      console.log('Group deleted successfully from database');
    } catch (error) {
      console.error('Error deleting group:', error);
      // Re-throw the error so it can be handled by the calling function
      throw error;
    }
  }
  
  static async findById(id: string): Promise<Group | null> {
    try {
      // Get current user
      const { data: { user }, error: authError } = await supabase.auth.getUser();
      
      // If there's an auth error, return null instead of throwing error
      if (authError) {
        console.warn('Authentication error:', authError.message);
        return null;
      }
      
      // If no user, return null instead of throwing error
      if (!user) {
        console.warn('No authenticated user found, returning null for group find');
        return null;
      }

      const { data, error } = await supabase
        .from('groups')
        .select('*')
        .eq('id', id)
        .eq('user_id', user.id)
        .single();
      
      if (error) {
        if (error.code === 'PGRST116') {
          // No rows found
          return null;
        }
        throw error;
      }
      
      return data || null;
    } catch (error) {
      console.error('Error finding group:', error);
      // Return null instead of throwing error to prevent blank page
      return null;
    }
  }
}