import { useState } from 'react';
import { Expense } from '@/entities/Expense';
import { formatCurrency } from '@/utils/currency';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Badge } from '@/components/ui/badge';
import { Input } from '@/components/ui/input';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { 
  Receipt, 
  Search, 
  Filter, 
  Trash2, 
  MapPin, 
  Image,
  Calendar,
  DollarSign
} from 'lucide-react';
import { motion, AnimatePresence } from 'framer-motion';

export default function ExpensesList({ 
  expenses, 
  members, 
  categories, 
  group,
  onExpenseDeleted 
}: any) {
  const [searchTerm, setSearchTerm] = useState('');
  const [categoryFilter, setCategoryFilter] = useState('all');
  const [sortBy, setSortBy] = useState('date');

  // Filter and sort expenses
  const filteredExpenses = expenses
    .filter((expense: any) => {
      const matchesSearch = expense.description.toLowerCase().includes(searchTerm.toLowerCase());
      const matchesCategory = categoryFilter === 'all' || expense.category === categoryFilter;
      return matchesSearch && matchesCategory;
    })
    .sort((a: any, b: any) => {
      switch (sortBy) {
        case 'amount':
          return b.amount - a.amount;
        case 'category':
          return a.category.localeCompare(b.category);
        default:
          return new Date(b.created_date).getTime() - new Date(a.created_date).getTime();
      }
    });

  const handleDeleteExpense = async (expenseId: string) => {
    if (window.confirm('Are you sure you want to delete this expense?')) {
      try {
        await Expense.delete(expenseId);
        await onExpenseDeleted();
      } catch (error) {
        console.error('Error deleting expense:', error);
      }
    }
  };

  const getMemberName = (memberId: string) => {
    return members.find((m: any) => m.id === memberId)?.name || 'Unknown';
  };

  const getCategoryInfo = (categoryName: string) => {
    return categories.find((c: any) => c.name === categoryName) || categories[categories.length - 1];
  };

  if (expenses.length === 0) {
    return (
      <Card className="bg-white/5 border-white/10 backdrop-blur-xl">
        <CardContent className="pt-6">
          <div className="text-center py-12">
            <Receipt className="h-12 w-12 text-slate-400 mx-auto mb-4" />
            <h3 className="text-white text-lg font-medium mb-2">No expenses yet</h3>
            <p className="text-slate-400">
              Add your first expense to get started tracking group spending
            </p>
          </div>
        </CardContent>
      </Card>
    );
  }

  return (
    <Card className="bg-gray-950/50 border-gray-800/50 backdrop-blur-xl">
      <CardHeader>
        <CardTitle className="text-white flex items-center gap-2">
          <Receipt className="h-5 w-5 text-cyan-400" />
          Expenses ({filteredExpenses.length})
        </CardTitle>
      </CardHeader>
      <CardContent className="space-y-4">
        {/* Filters and Search */}
        <div className="flex flex-col sm:flex-row gap-3">
          <div className="relative flex-1">
            <Search className="absolute left-3 top-3 h-4 w-4 text-slate-400" />
            <Input
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
              placeholder="Search expenses..."
              className="bg-white/5 border-white/10 text-white pl-10"
            />
          </div>
          
          <Select value={categoryFilter} onValueChange={setCategoryFilter}>
            <SelectTrigger className="bg-white/5 border-white/10 text-white w-full sm:w-40">
              <SelectValue />
            </SelectTrigger>
            <SelectContent className="bg-slate-900 border-white/10">
              <SelectItem value="all" className="text-white hover:bg-white/10">
                All Categories
              </SelectItem>
              {categories.map((cat: any) => {
                const IconComponent = cat.icon;
                return (
                  <SelectItem key={cat.name} value={cat.name} className="text-white hover:bg-white/10">
                    <div className="flex items-center gap-2">
                      <IconComponent className="w-4 h-4" />
                      <span>{cat.name}</span>
                    </div>
                  </SelectItem>
                );
              })}
            </SelectContent>
          </Select>

          <Select value={sortBy} onValueChange={setSortBy}>
            <SelectTrigger className="bg-white/5 border-white/10 text-white w-full sm:w-32">
              <SelectValue />
            </SelectTrigger>
            <SelectContent className="bg-slate-900 border-white/10">
              <SelectItem value="date" className="text-white hover:bg-white/10">Date</SelectItem>
              <SelectItem value="amount" className="text-white hover:bg-white/10">Amount</SelectItem>
              <SelectItem value="category" className="text-white hover:bg-white/10">Category</SelectItem>
            </SelectContent>
          </Select>
        </div>

        {/* Expenses List */}
        <div className="space-y-3 max-h-96 overflow-y-auto">
          <AnimatePresence>
            {filteredExpenses.map((expense: any, index: number) => {
              const categoryInfo = getCategoryInfo(expense.category);
              const IconComponent = categoryInfo.icon;
              
              return (
                <motion.div
                  key={expense.id}
                  initial={{ opacity: 0, y: 20 }}
                  animate={{ opacity: 1, y: 0 }}
                  exit={{ opacity: 0, y: -20 }}
                  transition={{ delay: index * 0.05 }}
                  className="group p-4 bg-white/5 border border-white/10 rounded-lg hover:bg-white/10 transition-all"
                >
                  <div className="flex items-start justify-between">
                    <div className="flex-1">
                      <div className="flex items-center gap-2 mb-2">
                        <div 
                          className="w-6 h-6 rounded-full flex items-center justify-center"
                          style={{ backgroundColor: categoryInfo.color + '20', border: `2px solid ${categoryInfo.color}` }}
                        >
                          <IconComponent className="w-3 h-3" style={{ color: categoryInfo.color }} />
                        </div>
                        <h4 className="text-white font-medium">{expense.description}</h4>
                        {expense.is_personal && (
                          <Badge variant="outline" className="border-purple-400 text-purple-400">
                            Personal
                          </Badge>
                        )}
                      </div>
                      
                      <div className="flex flex-wrap items-center gap-4 text-sm text-slate-400">
                        <span className="flex items-center gap-1">
                          <Calendar className="h-3 w-3" />
                          {new Date(expense.created_date).toLocaleDateString()}
                        </span>
                        
                        <span className="flex items-center gap-1">
                          <DollarSign className="h-3 w-3" />
                          {formatCurrency(expense.amount, group?.currency || 'USD')}
                        </span>
                        
                        {expense.location && (
                          <span className="flex items-center gap-1">
                            <MapPin className="h-3 w-3" />
                            {expense.location.place_name || 'Location'}
                          </span>
                        )}
                        
                        {expense.receipt_image_url && (
                          <span className="flex items-center gap-1">
                            <Image className="h-3 w-3" />
                            Receipt
                          </span>
                        )}
                      </div>

                      {/* Participants */}
                      {!expense.is_personal && expense.participant_member_ids?.length > 0 && (
                        <div className="mt-2">
                          <p className="text-xs text-slate-400 mb-1">Shared with:</p>
                          <div className="flex flex-wrap gap-1">
                            {expense.participant_member_ids.map((memberId: string) => (
                              <Badge key={memberId} variant="outline" className="text-xs border-slate-600 text-slate-300">
                                {getMemberName(memberId)}
                              </Badge>
                            ))}
                          </div>
                        </div>
                      )}

                      {/* Payers */}
                      {expense.payer_amounts && Object.keys(expense.payer_amounts).length > 0 && (
                        <div className="mt-2">
                          <p className="text-xs text-slate-400 mb-1">Paid by:</p>
                          <div className="flex flex-wrap gap-2">
                            {Object.entries(expense.payer_amounts).map(([memberId, amount]: [string, any]) => (
                              <span key={memberId} className="text-xs text-green-400">
                                {getMemberName(memberId)}: {formatCurrency(amount, group?.currency || 'USD')}
                              </span>
                            ))}
                          </div>
                        </div>
                      )}

                      {/* Notes */}
                      {expense.notes && (
                        <div className="mt-2">
                          <p className="text-xs text-slate-400">{expense.notes}</p>
                        </div>
                      )}
                    </div>

                    <div className="flex flex-col items-end gap-2">
                      <div className="text-right">
                        <p className="text-xl font-bold text-white">
                          {formatCurrency(expense.amount, group?.currency || 'USD')}
                        </p>
                        <p className="text-xs text-slate-400">{categoryInfo.name}</p>
                      </div>
                      
                      <Button
                        size="sm"
                        variant="ghost"
                        onClick={() => handleDeleteExpense(expense.id)}
                        className="text-red-400 hover:text-red-300 hover:bg-red-500/10 opacity-0 group-hover:opacity-100 transition-opacity"
                      >
                        <Trash2 className="h-4 w-4" />
                      </Button>
                    </div>
                  </div>
                </motion.div>
              );
            })}
          </AnimatePresence>
        </div>

        {filteredExpenses.length === 0 && (
          <div className="text-center py-8">
            <Filter className="h-8 w-8 text-slate-400 mx-auto mb-2" />
            <p className="text-slate-400">No expenses match your filters</p>
          </div>
        )}
      </CardContent>
    </Card>
  );
}