import { formatCurrency } from '@/utils/currency';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Progress } from '@/components/ui/progress';
import { Badge } from '@/components/ui/badge';
import { 
  PieChart, 
  TrendingUp, 
  TrendingDown, 
  Calendar, 
  DollarSign,
  Target,
  Award,
  User
} from 'lucide-react';
import { motion } from 'framer-motion';

interface AnalyticsProps {
  expenses: any[];
  members: any[];
  categories: any[];
  group: any;
}

export default function Analytics({ 
  expenses, 
  members, 
  categories, 
  group
}: AnalyticsProps) {
  // Calculate various metrics
  const totalSpent = expenses.reduce((sum, e) => sum + e.amount, 0);
  const personalSpent = expenses.filter(e => e.is_personal).reduce((sum, e) => sum + e.amount, 0);
  const groupSpent = totalSpent - personalSpent;
  
  // Category breakdown
  const categorySpending = categories.map(cat => {
    const amount = expenses
      .filter(e => e.category === cat.name)
      .reduce((sum, e) => sum + e.amount, 0);
    return { 
      ...cat, 
      amount, 
      percentage: totalSpent > 0 ? (amount / totalSpent) * 100 : 0 
    };
  }).filter(c => c.amount > 0).sort((a, b) => b.amount - a.amount);

  // Member spending analysis - enhanced version
  const memberAnalysis = members.map(member => {
    // Calculate total amount paid by this member
    const memberExpenses = expenses.filter(e => 
      e.payer_amounts && e.payer_amounts[member.id] > 0
    );
    const totalPaid = memberExpenses.reduce((sum, e) => sum + (e.payer_amounts[member.id] || 0), 0);
    
    // Calculate total amount owed by this member for expenses they participated in
    const participatedExpenses = expenses.filter(e => 
      e.participant_member_ids && e.participant_member_ids.includes(member.id)
    );
    
    // For each expense they participated in, calculate their share
    let totalOwed = 0;
    participatedExpenses.forEach(expense => {
      if (expense.paid_by_group_budget) {
        // If paid by group budget, they don't owe anything
        return;
      }
      
      if (expense.is_personal) {
        // For personal expenses, only the specific payer owes (not necessarily the admin)
        // If this member is not the payer, they don't owe anything for personal expenses
        if (!(expense.payer_amounts && expense.payer_amounts[member.id] > 0)) {
          return;
        }
        // If this member is the payer of a personal expense, they owe the full amount to themselves
        // (which effectively means they don't owe anything since it's their own expense)
        return;
      }
      
      if (expense.participant_member_ids?.length > 0) {
        // Split equally among participants
        const share = expense.amount / expense.participant_member_ids.length;
        totalOwed += share;
      } else {
        // If no participants specified, assume it's just for the payer
        if (expense.payer_amounts && expense.payer_amounts[member.id] > 0) {
          // They paid it, so they don't owe themselves
          return;
        }
      }
    });
    
    // Calculate net balance (paid - owed)
    const netBalance = totalPaid - totalOwed;
    
    const expenseCount = memberExpenses.length;
    
    return {
      ...member,
      totalPaid,
      totalOwed,
      netBalance,
      expenseCount,
      avgExpense: expenseCount > 0 ? totalPaid / expenseCount : 0
    };
  }).sort((a, b) => b.totalPaid - a.totalPaid);

  // Time-based analysis (last 7 days vs older)
  const now = new Date();
  const sevenDaysAgo = new Date(now.getTime() - 7 * 24 * 60 * 60 * 1000);
  
  const recentExpenses = expenses.filter(e => new Date(e.created_date) >= sevenDaysAgo);
  const olderExpenses = expenses.filter(e => new Date(e.created_date) < sevenDaysAgo);
  
  const recentSpending = recentExpenses.reduce((sum, e) => sum + e.amount, 0);
  const dailyAverage = recentExpenses.length > 0 ? recentSpending / 7 : 0;

  // Budget efficiency
  const budgetUsed = (group?.budget || 0) > 0 ? (groupSpent / (group?.budget || 0)) * 100 : 0;
  const budgetStatus = budgetUsed > 100 ? 'over' : budgetUsed > 90 ? 'warning' : 'good';

  return (
    <div className="space-y-6">
      {/* Overview Stats */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4">
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ delay: 0.1 }}
        >
          <Card className="bg-gradient-to-r from-blue-500/20 to-cyan-500/20 border-blue-500/30 backdrop-blur-xl">
            <CardContent className="pt-6">
              <div className="flex items-center justify-between">
                <div>
                  <p className="text-blue-400 text-sm font-medium">Total Expenses</p>
                  <p className="text-2xl font-bold text-white">{expenses.length}</p>
                </div>
                <PieChart className="h-8 w-8 text-blue-400" />
              </div>
            </CardContent>
          </Card>
        </motion.div>

        <motion.div
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ delay: 0.2 }}
        >
          <Card className="bg-gradient-to-r from-green-500/20 to-emerald-500/20 border-green-500/30 backdrop-blur-xl">
            <CardContent className="pt-6">
              <div className="flex items-center justify-between">
                <div>
                  <p className="text-green-400 text-sm font-medium">Daily Average</p>
                  <p className="text-2xl font-bold text-white">{formatCurrency(dailyAverage, group?.currency || 'USD', 'symbol')}</p>
                </div>
                <Calendar className="h-8 w-8 text-green-400" />
              </div>
            </CardContent>
          </Card>
        </motion.div>

        <motion.div
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ delay: 0.3 }}
        >
          <Card className="bg-gradient-to-r from-purple-500/20 to-pink-500/20 border-purple-500/30 backdrop-blur-xl">
            <CardContent className="pt-6">
              <div className="flex items-center justify-between">
                <div>
                  <p className="text-purple-400 text-sm font-medium">Avg per Person</p>
                  <p className="text-2xl font-bold text-white">
                    {formatCurrency(members.length > 0 ? (groupSpent / members.length) : 0, group?.currency || 'USD', 'symbol')}
                  </p>
                </div>
                <DollarSign className="h-8 w-8 text-purple-400" />
              </div>
            </CardContent>
          </Card>
        </motion.div>

        <motion.div
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ delay: 0.4 }}
        >
          <Card className={`bg-gradient-to-r ${budgetStatus === 'over' ? 'from-red-500/20 to-orange-500/20 border-red-500/30' : budgetStatus === 'warning' ? 'from-yellow-500/20 to-orange-500/20 border-yellow-500/30' : 'from-green-500/20 to-emerald-500/20 border-green-500/30'} backdrop-blur-xl`}>
            <CardContent className="pt-6">
              <div className="flex items-center justify-between">
                <div>
                  <p className={`text-sm font-medium ${budgetStatus === 'over' ? 'text-red-400' : budgetStatus === 'warning' ? 'text-yellow-400' : 'text-green-400'}`}>
                    Budget Used
                  </p>
                  <p className={`text-2xl font-bold ${budgetStatus === 'over' ? 'text-red-400' : 'text-white'}`}>
                    {budgetUsed.toFixed(1)}%
                  </p>
                </div>
                <Target className={`h-8 w-8 ${budgetStatus === 'over' ? 'text-red-400' : budgetStatus === 'warning' ? 'text-yellow-400' : 'text-green-400'}`} />
              </div>
            </CardContent>
          </Card>
        </motion.div>
      </div>

      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
        {/* Category Spending */}
        <motion.div
          initial={{ opacity: 0, x: -20 }}
          animate={{ opacity: 1, x: 0 }}
          transition={{ delay: 0.5 }}
        >
          <Card className="bg-white/5 border-white/10 backdrop-blur-xl">
            <CardHeader>
              <CardTitle className="text-white">Spending by Category</CardTitle>
            </CardHeader>
            <CardContent className="space-y-4">
              {categorySpending.length > 0 ? (
                categorySpending.map((cat, index) => {
                  const IconComponent = cat.icon;
                  return (
                    <motion.div
                      key={cat.name}
                      initial={{ opacity: 0, x: -10 }}
                      animate={{ opacity: 1, x: 0 }}
                      transition={{ delay: 0.6 + index * 0.1 }}
                      className="space-y-2"
                    >
                      <div className="flex items-center justify-between">
                        <div className="flex items-center gap-2">
                          <div 
                            className="w-8 h-8 rounded-full flex items-center justify-center"
                            style={{ backgroundColor: cat.color + '20', border: `2px solid ${cat.color}30` }}
                          >
                            <IconComponent className="w-4 h-4" style={{ color: cat.color }} />
                          </div>
                          <span className="text-white font-medium">{cat.name}</span>
                          <Badge variant="outline" className="border-slate-600 text-slate-300">
                            {expenses.filter(e => e.category === cat.name).length} expenses
                          </Badge>
                        </div>
                        <div className="text-right">
                          <span className="text-white font-semibold">{formatCurrency(cat.amount, group?.currency || 'USD', 'symbol')}</span>
                          <span className="text-slate-400 text-sm ml-2">
                            {cat.percentage.toFixed(1)}%
                          </span>
                        </div>
                      </div>
                      <Progress value={cat.percentage} className="h-2 bg-slate-800">
                        <div 
                          className="h-full rounded-full transition-all"
                          style={{ 
                            width: `${cat.percentage}%`, 
                            backgroundColor: cat.color 
                          }}
                        />
                      </Progress>
                    </motion.div>
                  );
                })
              ) : (
                <p className="text-slate-400 text-center py-8">No category data available</p>
              )}
            </CardContent>
          </Card>
        </motion.div>

        {/* Individual Member Spending Totals */}
        <motion.div
          initial={{ opacity: 0, x: 20 }}
          animate={{ opacity: 1, x: 0 }}
          transition={{ delay: 0.6 }}
        >
          <Card className="bg-white/5 border-white/10 backdrop-blur-xl">
            <CardHeader>
              <CardTitle className="text-white flex items-center gap-2">
                <User className="h-5 w-5 text-blue-400" />
                Individual Member Spending
              </CardTitle>
            </CardHeader>
            <CardContent className="space-y-4">
              {memberAnalysis.map((member, index) => (
                <motion.div
                  key={member.id}
                  initial={{ opacity: 0, y: 10 }}
                  animate={{ opacity: 1, y: 0 }}
                  transition={{ delay: 0.7 + index * 0.1 }}
                  className="flex items-center justify-between p-3 bg-white/5 rounded-lg"
                >
                  <div className="flex items-center gap-3">
                    <div className="relative">
                      <div 
                        className="w-10 h-10 rounded-full flex items-center justify-center text-sm font-bold text-black"
                        style={{ backgroundColor: member.avatar_color || '#00fff0' }}
                      >
                        {member.name.charAt(0).toUpperCase()}
                      </div>
                      {index === 0 && (
                        <div className="absolute -top-1 -right-1">
                          <Award className="h-4 w-4 text-yellow-400" />
                        </div>
                      )}
                    </div>
                    <div>
                      <p className="text-white font-medium">{member.name}</p>
                      <div className="flex items-center gap-2 text-slate-400 text-sm">
                        <span>Paid: {formatCurrency(member.totalPaid, group?.currency || 'USD', 'symbol')}</span>
                        <span>•</span>
                        <span>Owes: {formatCurrency(member.totalOwed, group?.currency || 'USD', 'symbol')}</span>
                      </div>
                    </div>
                  </div>
                  <div className="text-right">
                    <p className={`font-semibold ${member.netBalance >= 0 ? 'text-green-400' : 'text-red-400'}`}>
                      {member.netBalance >= 0 ? '+' : ''}{formatCurrency(member.netBalance, group?.currency || 'USD', 'symbol')}
                    </p>
                    <p className="text-slate-400 text-sm">
                      {member.expenseCount} expenses
                    </p>
                  </div>
                </motion.div>
              ))}
            </CardContent>
          </Card>
        </motion.div>
      </div>

      {/* Recent Activity */}
      <motion.div
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ delay: 0.8 }}
      >
        <Card className="bg-white/5 border-white/10 backdrop-blur-xl">
          <CardHeader>
            <CardTitle className="text-white">Recent Activity (Last 7 Days)</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
              <div className="text-center">
                <p className="text-3xl font-bold text-cyan-400">{recentExpenses.length}</p>
                <p className="text-slate-400 text-sm">New Expenses</p>
              </div>
              <div className="text-center">
                <p className="text-3xl font-bold text-green-400">{formatCurrency(recentSpending, group?.currency || 'USD', 'symbol')}</p>
                <p className="text-slate-400 text-sm">Total Spent</p>
              </div>
              <div className="text-center">
                <p className="text-3xl font-bold text-purple-400">{formatCurrency(dailyAverage, group?.currency || 'USD', 'symbol')}</p>
                <p className="text-slate-400 text-sm">Daily Average</p>
              </div>
            </div>
            
            {recentExpenses.length > 0 && (
              <div className="mt-6">
                <h4 className="text-white font-medium mb-3">Recent Trends</h4>
                <div className="space-y-2">
                  {recentSpending > (olderExpenses.reduce((sum, e) => sum + e.amount, 0) / Math.max(olderExpenses.length, 1)) && (
                    <div className="flex items-center gap-2 text-sm text-orange-400">
                      <TrendingUp className="h-4 w-4" />
                      Spending has increased recently
                    </div>
                  )}
                  {recentExpenses.length > 3 && (
                    <div className="flex items-center gap-2 text-sm text-green-400">
                      <Calendar className="h-4 w-4" />
                      High activity in the past week
                    </div>
                  )}
                </div>
              </div>
            )}
          </CardContent>
        </Card>
      </motion.div>
    </div>
  );
}