import { useState } from 'react';
import { Expense } from '@/entities/Expense';
import { formatCurrency } from '@/utils/currency';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { RadioGroup, RadioGroupItem } from '@/components/ui/radio-group';
import { Label } from '@/components/ui/label';
import { Textarea } from '@/components/ui/textarea';
import { Checkbox } from '@/components/ui/checkbox';
import { 
  Plus, 
  MapPin, 
  Users,
  Loader2,
  User,
  Wallet,
  Split,
  Trash2,
  Edit2,
  X
} from 'lucide-react';
import { motion, AnimatePresence } from 'framer-motion';
import ReceiptPreview from './ReceiptPreview';

interface Location {
  lat: number;
  lng: number;
  address: string;
  place_name: string;
}

interface Payer {
  memberId: string;
  amount: string;
}

interface FormData {
  description: string;
  amount: string;
  category: string;
  payment_method: 'personal' | 'group_budget' | 'custom_payers';
  participant_member_ids: string[];
  payers: Payer[];
  notes: string;
  location: Location | null;
  receipt_image_url: string | null;
}

interface ReceiptPreviewData {
  description: string;
  amount: number;
  category: string;
  date?: string | null;
  items?: string[];
  paymentMethod?: string | null;
}

export default function ExpenseForm({ 
  groupId, 
  members, 
  categories, 
  group,
  onExpenseAdded 
}: any) {
  const [formData, setFormData] = useState<FormData>({
    description: '',
    amount: '',
    category: categories[0]?.name || 'Other',
    payment_method: 'custom_payers',
    participant_member_ids: members.map((m: any) => m.id),
    payers: [],
    notes: '',
    location: null,
    receipt_image_url: null,
  });
  
  const [loading, setLoading] = useState(false);
  const [gettingLocation, setGettingLocation] = useState(false);
  const [locationStatus, setLocationStatus] = useState('Tag Location');
  const [locationError, setLocationError] = useState<string | null>(null);
  const [showAdvanced, setShowAdvanced] = useState(false);
  const [editingLocation, setEditingLocation] = useState(false);
  const [manualLocation, setManualLocation] = useState("");
  const [receiptPreview, setReceiptPreview] = useState<ReceiptPreviewData | null>(null);

  const handleInputChange = (field: string, value: any) => {
    // Special handling for amount field to ensure proper decimal formatting
    if (field === 'amount') {
      // Remove any non-numeric characters except decimal point
      let formattedValue = value.toString().replace(/[^0-9.]/g, '');
      
      // Ensure only one decimal point
      const parts = formattedValue.split('.');
      if (parts.length > 2) {
        formattedValue = parts[0] + '.' + parts.slice(1).join('');
      }
      
      // Limit to 2 decimal places
      if (parts[1] && parts[1].length > 2) {
        formattedValue = parts[0] + '.' + parts[1].substring(0, 2);
      }
      
      value = formattedValue;
    }
    
    setFormData(prev => {
      const newState = { ...prev, [field]: value };
      
      if (field === 'payment_method') {
        if (value === 'personal') {
          // Find the admin member, or fall back to current user, or first member
          const adminMember = members.find((m: any) => m.is_admin);
          const currentUserMember = members.find((m: any) => m.is_current_user);
          const defaultMember = adminMember || currentUserMember || members[0];
          
          newState.participant_member_ids = defaultMember ? [defaultMember.id] : [];
          newState.payers = [];
        } else if (value === 'group_budget') {
          newState.participant_member_ids = members.map((m: any) => m.id);
          newState.payers = [];
        } else if (value === 'custom_payers') {
          newState.participant_member_ids = members.map((m: any) => m.id);
        }
      }
      
      return newState;
    });
  };

  const handleParticipantToggle = (memberId: string) => {
    setFormData(prev => ({
      ...prev,
      participant_member_ids: prev.participant_member_ids.includes(memberId)
        ? prev.participant_member_ids.filter((id: string) => id !== memberId)
        : [...prev.participant_member_ids, memberId]
    }));
  };

  const handlePayerChange = (index: number, field: keyof Payer, value: string) => {
    // Special handling for amount field to ensure proper decimal formatting
    if (field === 'amount') {
      // Remove any non-numeric characters except decimal point
      let formattedValue = value.toString().replace(/[^0-9.]/g, '');
      
      // Ensure only one decimal point
      const parts = formattedValue.split('.');
      if (parts.length > 2) {
        formattedValue = parts[0] + '.' + parts.slice(1).join('');
      }
      
      // Limit to 2 decimal places
      if (parts[1] && parts[1].length > 2) {
        formattedValue = parts[0] + '.' + parts[1].substring(0, 2);
      }
      
      value = formattedValue;
    }
    
    setFormData(prev => {
      const newPayers = [...prev.payers];
      newPayers[index] = { ...newPayers[index], [field]: value };
      return { ...prev, payers: newPayers };
    });
  };

  const addPayer = () => {
    setFormData(prev => ({
      ...prev,
      payers: [...prev.payers, { memberId: '', amount: '' }]
    }));
  };

  const removePayer = (index: number) => {
    const newPayers = formData.payers.filter((_: any, i: number) => i !== index);
    setFormData(prev => ({ ...prev, payers: newPayers }));
  };

  const getCurrentLocation = () => {
    setGettingLocation(true);
    setLocationStatus('Getting Location...');
    setLocationError(null);
    
    if (navigator.geolocation) {
      navigator.geolocation.getCurrentPosition(
        (position) => {
          const { latitude, longitude } = position.coords;
          
          // Create a location name based on coordinates
          // In a production app, you would want to reverse geocode these coordinates
          // to get a human-readable address
          const locationName = `Current Location`;
          
          setFormData(prev => ({ 
            ...prev, 
            location: { 
              lat: latitude, 
              lng: longitude, 
              address: `Lat: ${latitude.toFixed(4)}, Lng: ${longitude.toFixed(4)}`,
              place_name: locationName
            } 
          }));
          
          setGettingLocation(false);
          setLocationStatus('Tag Location');
          setLocationError(null);
        },
        (error) => {
          console.error('Error getting location:', error);
          console.error('Error code:', error.code);
          console.error('Error message:', error.message);
          
          // Even if we can't get a precise location, we can still tag it as "Current Location"
          if (error.code === error.TIMEOUT || error.code === error.POSITION_UNAVAILABLE) {
            setFormData(prev => ({ 
              ...prev, 
              location: { 
                lat: 0, 
                lng: 0, 
                address: "Location services unavailable",
                place_name: "Current Location"
              } 
            }));
            setGettingLocation(false);
            setLocationStatus('Tag Location');
            setLocationError(null);
            return;
          }
          
          let errorMessage = "Unable to get your location. ";
          switch(error.code) {
            case error.PERMISSION_DENIED:
              errorMessage += "Permission denied. Please enable location access in your browser settings.";
              break;
            case error.POSITION_UNAVAILABLE:
              errorMessage += "Location information is unavailable.";
              break;
            case error.TIMEOUT:
              errorMessage += "The request to get user location timed out.";
              break;
            default:
              errorMessage += `An unknown error occurred (code: ${error.code}). ${error.message || 'No additional information available.'}`;
              break;
          }
          setLocationError(errorMessage);
          setGettingLocation(false);
          setLocationStatus('Tag Location');
        },
        {
          enableHighAccuracy: true,
          timeout: 10000,
          maximumAge: 300000
        }
      );
    } else {
      const errorMessage = "Geolocation is not supported by this browser.";
      setLocationError(errorMessage);
      setGettingLocation(false);
      setLocationStatus('Tag Location');
    }
  };

  const confirmReceiptDetails = () => {
    if (receiptPreview) {
      setFormData(prev => ({
        ...prev,
        description: receiptPreview.description || prev.description,
        amount: receiptPreview.amount?.toString() || prev.amount,
        category: receiptPreview.category || prev.category,
        // Add notes with additional extracted information
        notes: buildNotesFromExtractedData(receiptPreview, prev.notes)
      }));
      setReceiptPreview(null);
    }
  };

  // Helper function to build notes from extracted data
  const buildNotesFromExtractedData = (data: ReceiptPreviewData, existingNotes: string): string => {
    const notesParts: string[] = [];
    
    if (existingNotes) {
      notesParts.push(existingNotes);
    }
    
    if (data.date) {
      notesParts.push(`Date: ${data.date}`);
    }
    
    if (data.items && data.items.length > 0) {
      notesParts.push(`Items: ${data.items.join(', ')}`);
    }
    
    if (data.paymentMethod) {
      notesParts.push(`Payment: ${data.paymentMethod}`);
    }
    
    return notesParts.join('; ');
  };

  const rejectReceiptDetails = () => {
    setReceiptPreview(null);
  };

  const handleFileUpload = (e: React.ChangeEvent<HTMLInputElement>) => {
    // File upload is no longer needed since we're removing the AI scanner
    e.target.value = '';
  };

  const handleManualLocationSave = () => {
    if (manualLocation.trim() && formData.location) {
      setFormData(prev => ({
        ...prev,
        location: {
          ...prev.location,
          place_name: manualLocation,
        }
      }));
      setEditingLocation(false);
      setLocationError(null);
    }
  };

  const startEditingLocationName = () => {
    if (formData.location) {
      setManualLocation(formData.location.place_name);
      setEditingLocation(true);
    }
  };

  const removeLocation = () => {
    setFormData(prev => ({
      ...prev,
      location: null
    }));
    setEditingLocation(false);
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    const expectedAmount = Math.round((parseFloat(formData.amount) || 0) * 100) / 100;
    
    if (!formData.description.trim() || !formData.amount || isNaN(expectedAmount) || expectedAmount <= 0) {
      console.log('Form validation failed: description or amount missing/invalid');
      return;
    }

    setLoading(true);
    console.log('Starting expense creation process');
    
    try {
      // Prepare expense data with all required fields
      const expenseData: any = {
        group_id: groupId,
        description: formData.description.trim(),
        amount: expectedAmount,
        currency: group?.currency || 'USD',
        category: formData.category,
        location: formData.location,
        receipt_image_url: formData.receipt_image_url,
        notes: formData.notes,
        is_personal: false,
        paid_by_group_budget: false,
        participant_member_ids: [...formData.participant_member_ids],
      };

      // Set payment method specific fields
      if (formData.payment_method === 'personal') {
        expenseData.is_personal = true;
        expenseData.paid_by_group_budget = false;
        // For personal expenses, if no participants are selected, assign to admin member
        if (expenseData.participant_member_ids.length === 0) {
          const adminMember = members.find((m: any) => m.is_admin);
          const currentUserMember = members.find((m: any) => m.is_current_user);
          const defaultMember = adminMember || currentUserMember || members[0];
          
          if (defaultMember) {
            expenseData.participant_member_ids = [defaultMember.id];
          }
        }
      } else if (formData.payment_method === 'group_budget') {
        expenseData.is_personal = false;
        expenseData.paid_by_group_budget = true;
      } else if (formData.payment_method === 'custom_payers') {
        expenseData.is_personal = false;
        expenseData.paid_by_group_budget = false;
        // Build payer amounts from form data with proper rounding
        const payerAmounts: { [key: string]: number } = {};
        formData.payers.forEach((payer: Payer) => {
          if (payer.memberId && parseFloat(payer.amount) > 0) {
            const memberId = payer.memberId;
            const amount = Math.round((parseFloat(payer.amount) || 0) * 100) / 100;
            const currentAmount = payerAmounts[memberId] || 0;
            payerAmounts[memberId] = Math.round((currentAmount + amount) * 100) / 100;
          }
        });
        expenseData.payer_amounts = payerAmounts;
      }

      console.log('Prepared expense data:', expenseData);
      
      // Create the expense
      const createdExpense = await Expense.create(expenseData);
      console.log('Expense created successfully:', createdExpense);
      
      // Reset form to initial state
      setFormData({
        description: '', 
        amount: '', 
        category: categories[0]?.name || 'Other',
        payment_method: 'custom_payers',
        participant_member_ids: members.map((m: any) => m.id),
        payers: [],
        notes: '', 
        location: null, 
        receipt_image_url: null,
      });
      
      // Reset UI state
      setShowAdvanced(false);
      
      // Refresh the expense list
      console.log('Refreshing expense list...');
      await onExpenseAdded();
      console.log('Expense list refreshed successfully');
      
    } catch (error: unknown) {
      console.error('Error adding expense:', error);
      console.error('Full error object:', error);
      if (error instanceof Error) {
        alert(`Failed to add expense: ${error.message || 'Unknown error'}`);
      } else {
        alert('Failed to add expense: Unknown error');
      }
    } finally {
      setLoading(false);
    }
  };

  const totalPayerAmounts = formData.payers.reduce((sum: number, p: any) => {
    const amount = parseFloat(p.amount) || 0;
    // Round to 2 decimal places to avoid floating point precision issues
    return Math.round((sum + amount) * 100) / 100;
  }, 0);

  const expectedAmount = Math.round((parseFloat(formData.amount) || 0) * 100) / 100;
  const payerAmountsMatch = Math.abs(totalPayerAmounts - expectedAmount) < 0.01;

  const isSubmitDisabled = loading || 
    !formData.description.trim() || 
    !formData.amount || 
    isNaN(expectedAmount) || 
    expectedAmount <= 0 ||
    (formData.payment_method === 'custom_payers' && 
     formData.payers.length > 0 && 
     !payerAmountsMatch);
  
  const availableMembersForPayer = members.filter(
    (m: any) => !formData.payers.some((p: any) => p.memberId === m.id)
  );

  return (
    <Card className="bg-gray-950/50 border-gray-800/50 backdrop-blur-xl shadow-2xl h-full flex flex-col">
      <CardHeader>
        <CardTitle className="text-white flex items-center gap-3 text-xl">
          <div className="p-2 bg-gradient-to-r from-emerald-500/20 to-blue-500/20 rounded-lg">
            <Plus className="h-5 w-5 text-emerald-400" />
          </div>
          Add Expense
        </CardTitle>
      </CardHeader>
      <CardContent className="flex-grow flex flex-col">
        <form onSubmit={handleSubmit} className="space-y-4 flex-grow flex flex-col">
          <div className="space-y-4 flex-grow">
            <AnimatePresence>
              {receiptPreview && (
                <ReceiptPreview 
                  data={receiptPreview} 
                  onConfirm={confirmReceiptDetails} 
                  onReject={rejectReceiptDetails} 
                />
              )}
            </AnimatePresence>
            
            <div>
              <Label className="text-gray-400">Description</Label>
              <Input
                value={formData.description}
                onChange={(e) => handleInputChange('description', e.target.value)}
                placeholder="e.g., Dinner at The Grand"
                className="bg-gray-800/50 border-gray-700 text-white mt-1"
                required
              />
            </div>
            
            <div className="grid grid-cols-2 gap-4">
              <div>
                <Label className="text-gray-400">Amount</Label>
                <div className="relative mt-1">
                  <Input
                    type="number" 
                    step="0.01" 
                    value={formData.amount}
                    onChange={(e) => handleInputChange('amount', e.target.value)}
                    placeholder="0.00"
                    className="bg-gray-800/50 border-gray-700 text-white pl-3"
                    required
                  />
                </div>
              </div>
              <div>
                <Label className="text-gray-400">Category</Label>
                <Select value={formData.category} onValueChange={(value) => handleInputChange('category', value)}>
                  <SelectTrigger className="bg-gray-800/50 border-gray-700 text-white mt-1">
                    <SelectValue />
                  </SelectTrigger>
                  <SelectContent className="bg-gray-800 border-gray-700">
                    {categories.map((cat: any) => {
                      const IconComponent = cat.icon;
                      return (
                        <SelectItem key={cat.name} value={cat.name} className="text-white focus:bg-gray-700">
                          <div className="flex items-center gap-2">
                            <IconComponent className="w-4 h-4" />
                            <span>{cat.name}</span>
                          </div>
                        </SelectItem>
                      );
                    })}
                  </SelectContent>
                </Select>
              </div>
            </div>
            
            <div className="flex flex-wrap gap-2">
              <Button 
                type="button" 
                variant="outline" 
                size="sm" 
                onClick={getCurrentLocation} 
                disabled={gettingLocation} 
                className="border-blue-500/30 text-blue-400 hover:bg-blue-500/10 hover:border-blue-500/80"
              >
                {gettingLocation ? <Loader2 className="h-4 w-4 mr-2 animate-spin" /> : <MapPin className="h-4 w-4 mr-2" />}
                {locationStatus}
              </Button>
            </div>
            
            {locationError && (
              <div className="text-red-400 text-sm p-2 bg-red-900/20 border border-red-500/30 rounded-lg">
                {locationError}
              </div>
            )}
            
            <AnimatePresence>
            {editingLocation && !formData.location && (
              <motion.div 
                initial={{ opacity: 0, y: -10 }} 
                animate={{ opacity: 1, y: 0 }} 
                exit={{ opacity: 0, y: -10 }} 
                className="space-y-3"
              >
                <Label className="text-gray-400">Edit Location Name</Label>
                <div className="flex gap-2">
                  <Input 
                    value={manualLocation}
                    onChange={(e) => setManualLocation(e.target.value)}
                    className="bg-gray-800/50 border-gray-700 text-white"
                    placeholder="Enter place name"
                  />
                  <Button 
                    type="button" 
                    onClick={handleManualLocationSave}
                    disabled={!manualLocation.trim()}
                    className="bg-blue-600 hover:bg-blue-700 disabled:opacity-50"
                  >
                    Save
                  </Button>
                  <Button 
                    type="button" 
                    variant="outline" 
                    onClick={() => {
                      setEditingLocation(false);
                      setManualLocation("");
                      setLocationError(null);
                    }}
                    className="border-gray-600 text-gray-300 hover:bg-gray-800"
                  >
                    Cancel
                  </Button>
                </div>
              </motion.div>
            )}
            </AnimatePresence>
            
            <AnimatePresence>
            {formData.location && !editingLocation && (
              <motion.div 
                initial={{ opacity: 0, y: -10 }} 
                animate={{ opacity: 1, y: 0 }} 
                exit={{ opacity: 0, y: -10 }} 
                className="flex items-center justify-between gap-2 p-3 bg-blue-500/10 border border-blue-500/20 rounded-lg"
              >
                <div className="flex items-center gap-2 overflow-hidden">
                    <MapPin className="h-4 w-4 text-blue-400 flex-shrink-0" />
                    <div>
                      <div className="text-blue-300 font-medium" title={formData.location.place_name}>
                        {formData.location.place_name}
                      </div>
                      {formData.location.address && formData.location.address !== formData.location.place_name && (
                        <div className="text-gray-400 text-xs" title={formData.location.address}>
                          {formData.location.address}
                        </div>
                      )}
                    </div>
                </div>
                <div className="flex gap-1">
                  <Button 
                      type="button" 
                      size="icon" 
                      variant="ghost" 
                      className="h-8 w-8 text-blue-400 hover:bg-blue-500/20" 
                      onClick={() => { setManualLocation(formData.location?.place_name || ''); setEditingLocation(true); }}
                  >
                      <Edit2 className="h-4 w-4"/>
                  </Button>
                  <Button 
                      type="button" 
                      size="icon" 
                      variant="ghost" 
                      className="h-8 w-8 text-red-400 hover:bg-red-500/10" 
                      onClick={removeLocation}
                  >
                      <X className="h-4 w-4" />
                  </Button>
                </div>
              </motion.div>
            )}
            </AnimatePresence>

            <AnimatePresence>
            {editingLocation && formData.location && (
              <motion.div 
                initial={{ opacity: 0, y: -10 }} 
                animate={{ opacity: 1, y: 0 }} 
                exit={{ opacity: 0, y: -10 }} 
                className="space-y-3"
              >
                <Label className="text-gray-400">Edit Location</Label>
                <div className="flex gap-2">
                  <Input 
                    value={manualLocation}
                    onChange={(e) => setManualLocation(e.target.value)}
                    className="bg-gray-800/50 border-gray-700 text-white"
                    placeholder="Enter place name or address"
                  />
                  <Button 
                    type="button" 
                    onClick={handleManualLocationSave}
                    disabled={!manualLocation.trim()}
                    className="bg-blue-600 hover:bg-blue-700 disabled:opacity-50"
                  >
                    Save
                  </Button>
                  <Button 
                    type="button" 
                    variant="outline" 
                    onClick={() => setEditingLocation(false)}
                    className="border-gray-600 text-gray-300 hover:bg-gray-800"
                  >
                    Cancel
                  </Button>
                </div>
              </motion.div>
            )}
            </AnimatePresence>

            <div>
              <Label className="text-gray-400 mb-3 block">Payment Method</Label>
              <RadioGroup 
                value={formData.payment_method} 
                onValueChange={(value) => handleInputChange('payment_method', value)}
                className="space-y-3"
              >
                <div className="flex items-center space-x-3 p-3 rounded-lg bg-gray-800/30 border border-gray-700/50">
                  <RadioGroupItem value="personal" id="personal" className="border-purple-500 text-purple-500" />
                  <Label htmlFor="personal" className="text-white flex items-center gap-2 cursor-pointer flex-1">
                    <User className="h-4 w-4 text-purple-400" />
                    <div>
                      <div className="font-medium">Personal Expense</div>
                      <div className="text-xs text-gray-400">Only for you, doesn't affect group balances</div>
                    </div>
                  </Label>
                </div>
                
                <div className="flex items-center space-x-3 p-3 rounded-lg bg-gray-800/30 border border-gray-700/50">
                  <RadioGroupItem value="group_budget" id="group_budget" className="border-green-500 text-green-500" />
                  <Label htmlFor="group_budget" className="text-white flex items-center gap-2 cursor-pointer flex-1">
                    <Wallet className="h-4 w-4 text-green-400" />
                    <div>
                      <div className="font-medium">Paid from Group Budget</div>
                      <div className="text-xs text-gray-400">Split equally among all members</div>
                    </div>
                  </Label>
                </div>
                
                <div className="flex items-center space-x-3 p-3 rounded-lg bg-gray-800/30 border border-gray-700/50">
                  <RadioGroupItem value="custom_payers" id="custom_payers" className="border-blue-500 text-blue-500" />
                  <Label htmlFor="custom_payers" className="text-white flex items-center gap-2 cursor-pointer flex-1">
                    <Split className="h-4 w-4 text-blue-400" />
                    <div>
                      <div className="font-medium">Custom Payers</div>
                      <div className="text-xs text-gray-400">Specify who paid what amounts</div>
                    </div>
                  </Label>
                </div>
              </RadioGroup>
            </div>
            
            <AnimatePresence>
            {formData.payment_method !== 'personal' && (
              <motion.div initial={{ opacity: 0, height: 0 }} animate={{ opacity: 1, height: 'auto' }} exit={{ opacity: 0, height: 0 }} className="space-y-4 overflow-hidden">
                <div>
                  <Label className="text-gray-400 mb-2 block"><Users className="h-4 w-4 inline mr-2" />Who Participated?</Label>
                  {formData.payment_method === 'group_budget' && (
                    <p className="text-xs text-gray-500 mb-2">Select members who should share this expense from the group budget</p>
                  )}
                  <div className="grid grid-cols-2 sm:grid-cols-3 gap-2">
                    {members.map((member: any) => (
                      <div key={member.id} className="flex items-center space-x-2">
                        <Checkbox 
                          id={`p-${member.id}`} 
                          checked={formData.participant_member_ids.includes(member.id)} 
                          onCheckedChange={() => handleParticipantToggle(member.id)}
                          disabled={false}
                        />
                        <Label htmlFor={`p-${member.id}`} className="text-white text-sm cursor-pointer">{member.name}</Label>
                      </div>
                    ))}
                  </div>
                </div>
              </motion.div>
            )}
            </AnimatePresence>

            <AnimatePresence>
            {formData.payment_method === 'custom_payers' && (
              <motion.div initial={{ opacity: 0, height: 0 }} animate={{ opacity: 1, height: 'auto' }} exit={{ opacity: 0, height: 0 }} className="space-y-4 overflow-hidden">
                <div>
                  <Label className="text-gray-400 mb-2 block">Who Paid How Much?</Label>
                  <div className="space-y-3">
                    {formData.payers.map((payer: any, index: number) => (
                      <motion.div 
                        key={index}
                        initial={{ opacity: 0, y: -10 }}
                        animate={{ opacity: 1, y: 0 }}
                        exit={{ opacity: 0, y: 10 }}
                        className="flex items-center gap-2"
                      >
                        <Select 
                          value={payer.memberId} 
                          onValueChange={(value) => handlePayerChange(index, 'memberId', value)}
                        >
                          <SelectTrigger className="bg-gray-800/50 border-gray-700 text-white w-1/2">
                            <SelectValue placeholder="Select member..." />
                          </SelectTrigger>
                          <SelectContent className="bg-gray-800 border-gray-700">
                             {payer.memberId && !availableMembersForPayer.find((m: any) => m.id === payer.memberId) && (
                                <SelectItem value={payer.memberId} className="text-white focus:bg-gray-700">
                                    {members.find((m: any) => m.id === payer.memberId)?.name}
                                </SelectItem>
                             )}
                            {availableMembersForPayer.map((member: any) => (
                              <SelectItem key={member.id} value={member.id} className="text-white focus:bg-gray-700">
                                {member.name}
                              </SelectItem>
                            ))}
                          </SelectContent>
                        </Select>
                        <div className="relative flex-1">
                          <Input 
                            type="number" 
                            step="0.01" 
                            value={payer.amount} 
                            onChange={(e) => handlePayerChange(index, 'amount', e.target.value)} 
                            placeholder="0.00" 
                            className="bg-gray-800/50 border-gray-700 text-white pl-3"
                          />
                        </div>
                        <Button type="button" variant="ghost" size="icon" onClick={() => removePayer(index)} className="text-red-400 hover:bg-red-500/10 h-9 w-9">
                          <Trash2 className="h-4 w-4" />
                        </Button>
                      </motion.div>
                    ))}
                  </div>
                  <Button type="button" variant="outline" size="sm" onClick={addPayer} className="mt-3 border-dashed border-gray-600 text-gray-400 hover:text-white hover:border-gray-400">
                    <Plus className="h-4 w-4 mr-2" /> Add Payer
                  </Button>
                  {expectedAmount > 0 && !payerAmountsMatch && formData.payers.length > 0 && (
                    <p className="text-red-400 text-xs mt-2">
                      Payer amounts ({formatCurrency(totalPayerAmounts, group?.currency || 'USD', 'symbol')}) must equal total expense ({formatCurrency(expectedAmount, group?.currency || 'USD', 'symbol')}).
                    </p>
                  )}
                </div>
              </motion.div>
            )}
            </AnimatePresence>

            <Button type="button" variant="link" onClick={() => setShowAdvanced(!showAdvanced)} className="text-gray-400 hover:text-white px-0">
              {showAdvanced ? 'Hide' : 'Show'} Advanced Options
            </Button>
            
            <AnimatePresence>
              {showAdvanced && (
                <motion.div initial={{ opacity: 0, height: 0 }} animate={{ opacity: 1, height: 'auto' }} exit={{ opacity: 0, height: 0 }} className="space-y-4 overflow-hidden">
                  <div>
                    <Label className="text-gray-400">Notes (optional)</Label>
                    <Textarea 
                      value={formData.notes} 
                      onChange={(e) => handleInputChange('notes', e.target.value)} 
                      placeholder="Any details to remember..." 
                      className="bg-gray-800/50 border-gray-700 text-white mt-1" 
                      rows={2}
                    />
                  </div>
                </motion.div>
              )}
            </AnimatePresence>
          </div>
          
          <div className="mt-auto pt-4">
            <Button type="submit" disabled={isSubmitDisabled} className="w-full bg-gradient-to-r from-emerald-500 to-blue-500 hover:from-emerald-600 hover:to-blue-600 text-white border-none shadow-lg hover:shadow-emerald-500/25 transition-all duration-300 disabled:opacity-50">
              {loading ? <Loader2 className="h-4 w-4 mr-2 animate-spin" /> : <Plus className="h-4 w-4 mr-2" />}
              {loading ? 'Adding...' : 'Add Expense'}
            </Button>
          </div>
        </form>
      </CardContent>
    </Card>
  );
}